import { Component, Input } from '@angular/core';
import { TiFormComponent } from '@cloud/tiny3';
/**
 * 选择组 组件
 *
 * 配合[TpSelectItemComponent]{@link ../components/TpSelectitemComponent.html}使用
 *
 * <example-url>../tinyplus3demo/#/selectgroup/selectgroup-all</example-url>
 */
@Component({
    selector: 'tp-selectgroup',
    templateUrl: './selectgroup.html',
    providers: [TiFormComponent.getValueAccessor(TpSelectgroupComponent)]
})

export class TpSelectgroupComponent extends TiFormComponent {
    /**
     * 10.1.1 新增
     *
     * 选择组是否多选
     */
    @Input() multiple: boolean;
}
