import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

import { TiOverflowModule, TiSelectModule, TiTipModule } from '@cloud/tiny3';
import { TpIconModule } from '../icon/TpIconModule';
import { TpSubtitleComponent } from './TpSubtitleComponent';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RouterModule,
    TiOverflowModule,
    TiSelectModule,
    TiTipModule,
    TpIconModule
  ],
  exports: [TpSubtitleComponent],
  declarations: [TpSubtitleComponent]
})

export class TpSubtitleModule {}
export { TpSubtitleComponent, TpSubtitleItem} from './TpSubtitleComponent';
