import { Component, Input } from '@angular/core';
import { TiBaseComponent, TiLocale } from '@cloud/tiny3';
import { TpCountDownCode, TpCountDownInfos } from '../countdown/TpCountdownModule';

/**
 * 倒计时提示组件
 *
 * <example-url>../tinyplus3demo/#/countdownalert/countdownalert-all</example-url>
 */
@Component({
    selector: 'tp-countdownalert',
    templateUrl: './countdownalert.html',
    styleUrls: ['./countdownalert.less']
})

export class TpCountdownalertComponent extends TiBaseComponent {
    /**
     * 告警类型， 默认为error
     */
    @Input() type: 'success' | 'error' | 'warn' | 'prompt' = 'error';
    /**
     * 是否显示左侧提示类型图标
     */
    @Input() typeIcon: boolean = true;
    /**
     * 是否显示关闭按钮
     */
    @Input() closeIcon: boolean = true;
    /**
     * 是否显示countdownalert
     */
    @Input() open: boolean = false;
    /**
     * 显示多长时间后消失，默认不延迟消失，只在点击关闭按钮时消失
     */
    @Input() dismissOnTimeout: number;
    /**
     * 是否触发下拉类组件消失的事件，适用于countdownalert的呈现状态影响页面下拉类组件位置的场景
     */
    @Input() triggerScroll: boolean = false;
    /**
     * 自定义告警内容
     */
    @Input() label: string;
    /**
     * 倒计时时间信息（未配置label时需要定义）
     */
    @Input() infos: TpCountDownInfos;
    /**
     * 资源状态（未配置label时需要定义）
     */
    @Input() status:  TpCountDownCode;
    /**
     * 资源名称
     */
    @Input() resource: string;
    /**
     * 链接地址
     *
     * 包周期为续费时（status = hws_countdown_period_gracing | hws_countdown_period_frozen），服务需要传自定义续费链接；
     */
    @Input() linkHref: string;
    /**
     * 自定义客服联系电话号码
     * 10.1.2 版本新增
     */
    @Input() phoneNumber: string;

    /**
     * @ignore
     */
    public innerLabel: string = '';
    /**
     * @ignore
     */
    public showBuyLink: boolean;
    /**
     * @ignore
     */
    public linkText: string;

    ngOnInit(): void {
        super.ngOnInit();
        if (this.label) { return; }
        // 处理label
        const localeWords: any = TiLocale.getLocaleWords().tpCountdownalert;
        let timespan: string = '';
        if (this.infos) {
            timespan = this.getTimespan(this.infos, localeWords);
        }

        if (this.status) {
            const statusKey: string = this.status ? this.status.slice(14) : '';
            if (Object.keys(localeWords).indexOf(statusKey) >= 0) {
                this.innerLabel = localeWords[statusKey].replace(/{TS}/, String(timespan))
                    .replace(/{RN}/, this.resource || '');
            } else {
                // 状态正常，不需要显示alert
                this.open = false;
            }
        }

        // 处理充值续费链接：包周期为续费，服务需要传自定义链接；按需为充值，充值为固定链接
        if (this.status === 'hws_countdown_period_gracing' || this.status === 'hws_countdown_period_frozen') {
            this.showBuyLink = true;
            this.linkText = localeWords.renew;
        } else if (this.status === 'hws_countdown_ondemand_gracing' || this.status === 'hws_countdown_ondemand_frozen') {
            this.showBuyLink = true;
            this.linkText = localeWords.recharge;
            this.linkHref = 'https://account.huaweicloud.com/usercenter/#/userindex/balanceRecharge';
        }

        // 国际站 联系方式处理
        const intlContactPath: string = '/ticket/?#/ticketindex/createfeedback'; // 国际站联系客服路径
        const url: string = `${window.location.protocol}//${window.location.host}${intlContactPath}`;
        this.innerLabel = this.innerLabel.replace(
            /{contactLink}/,
            `<a class="tp-countdownalert-link" target="_blank" rel="noopener noreferrer"  href="${url}">${localeWords.contact_customer_service}</a>`);

        // 自定义客服联系电话号码处理
        this.innerLabel = this.innerLabel.replace(/{phoneNumber}/, this.phoneNumber || localeWords.phoneNumber);
    }

    private getTimespan(infos: TpCountDownInfos, localeWords: any): string {
        // tslint:disable-next-line: typedef
        const { day, hour, minute, second } = localeWords;
        if (infos.nextOperationRemainingDay) {
            return day.replace(/{d}/, infos.nextOperationRemainingDay);
        } else if (infos.nextOperationRemainingHour) {
            return hour.replace(/{h}/, infos.nextOperationRemainingHour)
                .replace(/{m}/, infos.nextOperationRemainingMinute);
        } else if (infos.nextOperationRemainingMinute) {
            return minute.replace(/{m}/, infos.nextOperationRemainingMinute);
        } else if (infos.nextOperationRemainingSecond) {
            return second;
        }

        return '';
    }
}
