import { TiPositionType } from '@cloud/tiny3';
import { Component, Input } from '@angular/core';
export interface TpButtons {
    /**
     * 按钮文本内容
     */
    label: string;
    /**
     * 按钮是否隐藏
     */
    hide?: boolean;
    /**
     * 按钮是否灰化
     */
    disabled?: boolean;
    /**
     * 按钮是否为主要按钮颜色
     */
    color?: 'danger' | 'primary';
    /**
     * 按钮点击事件
     */
    click?(): void; // 按钮点击事件
    /**
     * 按钮id
     */
    id?: string;
    /**
     * 按钮Tip内容
     */
    tip?: string;
    /**
     * 按钮Tip展开方向
     */
    tipPosition?: TiPositionType;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;
}
/**
 * @ignore
 * 购买浮层右侧按钮区域
 * <example-url>../tinyplus3demo/#/buylayer/buylayer-all</example-url>
 */
@Component({
    selector: 'tp-buylayer-right',
    templateUrl: './buylayer-right.html',
    styleUrls: ['./buylayer-right.less'],
    host: {
        '[class.tp-buylayer-right]': 'true'
    }
})

export class TpBuylayerRightComponent {
    /**
     * 浮层右侧按钮列表集合
     */
    @Input() buttons: Array<TpButtons> = [];
}
