import { Component, ElementRef, Renderer2, TemplateRef, ViewChild } from '@angular/core';
import { TiBaseComponent, TiModalRef, TiTipRef, TiTipService, TiTipShowInfo } from '@cloud/tiny3';
import { TpGoandbackService } from '../../services/goandback/TpGoandbackService';

/**
 * @ignore
 * 内部使用
 */
export interface TpRestrictMessage {
    label: string;
    href: string;
    link: string;
}

/**
 * 用户受限组件
 * <example-url>../tinyplus3demo/#/restrict/restrict-all</example-url>
 */
@Component({
    selector: 'tp-restrict',
    templateUrl: './restrict.html',
    styleUrls: ['./restrict.less']
})
export class TpRestrictComponent extends TiBaseComponent {

    /**
     * @ignore
     * tip 内容template
     */
    @ViewChild('tipContentTemplate', { static: false }) tipContentTemplate: TemplateRef<any>;

    /**
     * @ignore
     * alert 是否展示控制项
     */
    public open: boolean = false;
    /**
     * @ignore
     * alert 类型
     */
    public alertType: string = 'error';
    /**
     * @ignore
     * 内部alert类型图标
     */
    public typeIcon: boolean = true;
    /**
     * @ignore
     * 内部alert关闭图标
     */
    public closeIcon: boolean = false;
    /**
     * @ignore
     * alert 内容
     */
    public alertContent: string = '';
    /**
     * @ignore
     * tipcontent 内容
     */
    public tipContent: string = '';

    constructor(
        protected hostRef: ElementRef,
        protected renderer2: Renderer2,
        private tipService: TiTipService,
        private tpGoandbackService: TpGoandbackService
    ) {
        super(hostRef, renderer2);
    }

    /**
     * 显示提示块
     */
    public openAlert(content?: string): void {
        if (content) {
            this.alertContent = content;
            this.open = true;
        } else {
            this.open = false;
        }
    }

    /**
     * 给目标元素添加tip
     */
    public addTip(target: HTMLElement, content: string): TiTipRef {
        this.tipContent = content;
        if (this.tipContentTemplate) {
            return this.tipService.create(target, {
                trigger: 'mouse',
                showFn: (): TiTipShowInfo => {
                    return {
                        content: this.tipContentTemplate
                    };
                }
            });
        }
    }
    /**
     * 打开弹窗
     */
    public openMessage(messageOption: any, confirmFn?: (instances: TiModalRef, openAlertFn: Function) => void): void {
        let words: any;
        if (messageOption.firstMessage) {
            const message: TpRestrictMessage = this.getMessageInfo(messageOption.firstMessage);

            words = {
                firstMessageContent: message.label,
                firstMessageOkButtonText: message.link,
                path: message.href,
                ...messageOption
            };
        } else {
            words = messageOption;
        }
        this.tpGoandbackService.open({
            words,
            confirmFn,
            // 略过url添加params处理（restrict受限组件需略过tpGoandbackService中goToUrl方法对url的处理）
            addUrlParams: false
        });

    }

    //  message 弹窗的内容和按钮需要 alert 的内容转化
    // tslint:disable-next-line: prefer-function-over-method
    private getMessageInfo(content: string): TpRestrictMessage {
        let href: string;
        let link: string;
        let matchedString: string;
        // 返回数据中可能没有href属性，所有正则匹配两次
        // 提取出a 标签中的所有字符
        const reg: RegExp = /<a([^>]*)>(.*?)<\/a>/g;

        let regExeRes: RegExpExecArray = reg.exec(content);
        while (regExeRes && reg.lastIndex !== content.length) {
            regExeRes = reg.exec(content);
        }
        if (regExeRes) {
            // 提取a属性字符中的href内容
            const reg2: RegExp = /href=['"]?([^"]*)['"]?/g;
            matchedString = regExeRes[0];
            link = regExeRes[2];
            // 提取 href 的值
            const attrsStr: string = regExeRes[1];
            const regExeRes2: RegExpExecArray = reg2.exec(attrsStr);
            href = regExeRes2 ? regExeRes2[1] : '';
        }

        return {
            label: content.replace(matchedString, ''),
            link,
            href
        };
    }

}
