import { Pipe, PipeTransform } from '@angular/core';
/**
 * @ignore
 */
@Pipe({ name: 'rate' })
export class RatePipe implements PipeTransform {
    transform(value: Array<any>): Array<any> {
        return value.filter((item: any) => item.rate > 0 && item.rate < 1);
    }
}

/**
 * @ignore
 */
@Pipe({ name: 'notEqualTo0' })
export class NotEqualTo0Pipe implements PipeTransform {
    transform(value: Array<any>): Array<any> {
        return value.filter((item: any) => item.priceNum !== 0);
    }
}
/**
 * @ignore
 */
@Pipe({ name: 'notEqualTo01' })
export class NotEqualTo01Pipe implements PipeTransform {
    transform(value: Array<any>): Array<any> {
        return value.filter((item: any) => {
            if (item.id === 'no-discounts') {
                return item.priceNum === 0;
            } else {
                return item.priceNum !== 0;
            }
        });
    }
}
