import { Component, ContentChild, Input, TemplateRef } from "@angular/core";
import { TiBaseComponent } from '@cloud/tiny3';
/**
 * @ignore
 * 规范提出，云业务通用场景不会使用该组件，10.1.3起，在官网隐藏该组件。
 *
 * 智能提示组件
 *
 * <example-url>../tinyplus3demo/#/smartalert/smartalert-all</example-url>
 */
@Component({
    selector: 'tp-smartalert',
    templateUrl: 'smartalert.html',
    styleUrls: ['./smartalert.less']
})

export class TpSmartalertComponent extends TiBaseComponent {
    /**
     * 提示类型
     */
    @Input() type: 'prompt' | 'warn' | 'error' = 'prompt';
    /**
     * 样式大小
     */
    @Input() size: 'normal' | 'small' = 'normal';
    /**
     * 边框一直显示或是悬浮显示
     */
    @Input() border: 'always' | 'hover' = 'always';
    /**
     * @ignore 自定义左侧图标
     */
    @ContentChild('leftIcon', { static: true }) leftIconTemplate: TemplateRef<any>;
}
