import { Component, ElementRef, Renderer2 } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
import { TpGuidestepComponent } from './TpGuidestepComponent';

/**
 * TpGuidestepHeaderComponent 是单个小步骤模块组件的标题头
 */
@Component({
    selector: 'tp-guidestep-header',
    template: `<span><ng-content></ng-content></span>`,
    styleUrls: ['./guidestep-header.less'],
    host: {
        '[class.tp-guidestep-dot-complete]': 'guidestepComp.isComplete'
    }
})

export class TpGuidestepHeaderComponent extends TiBaseComponent {
    constructor(private elementRef: ElementRef, private renderer2: Renderer2, public guidestepComp: TpGuidestepComponent) {
        super(elementRef, renderer2);
    }
}
