import { Component, Input } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
/**
 * 卡片组件
 *
 * <example-url>../tinyplus3demo/#/card/card-all</example-url>
 */
@Component({
    selector: 'tp-card',
    template: `
      <ng-content ></ng-content>
    `,
    styleUrls: ['./card.less'],
    host: {
        '[class.tp-card-disabled]': 'disabled',
        '[class.tp-card-active]': 'active',
        '[attr.tabindex]': 'disabled? "-1":"0"'
    }
})

export class TpCardComponent extends TiBaseComponent {
    /**
     * 卡片禁用状态
     */
    @Input() disabled: boolean = false;
    /**
     * 卡片激活选中状态
     */
    @Input() active: boolean = false;
}
