import { Injectable } from '@angular/core';
import { TpRechargeModule } from './TpRechargeModule';
import { TiLocale, TiMessageType } from '@cloud/tiny3';
import { TpGoandbackService } from '../goandback/TpGoandbackService';

/**
 * 充值组件  recharge，使用该服务时需要引入模块TpRechargeModule，导入服务TpRechargeSerive,通过调用TpRechargeService.open(config)方法生成充值内容
 *
 * <example-url>../tinyplus3demo/#/recharge/recharge-all</example-url>
 */
@Injectable({
    providedIn: TpRechargeModule
})
export class TpRechargeService extends TpGoandbackService {
    protected words: any = TiLocale.getLocaleWords().tpRecharge;
    protected firstMessageType: TiMessageType = 'error';
}
