import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TpIconComponent } from './TpIconComponent';
import { TpIconlocalComponent } from './TpIconlocalComponent';

// 此模块不导入HttpClient，是因为懒加载模块中的HttpClientModule，会覆盖全局的。导致http拦截器失效。
// import { HttpClientModule } from '@angular/common/http';

@NgModule({
  imports: [CommonModule/* , HttpClientModule */],
  exports: [TpIconComponent, TpIconlocalComponent],
  declarations: [TpIconComponent, TpIconlocalComponent]
})
export class TpIconModule {}
export { TpIconComponent } from './TpIconComponent';
export { TpIconlocalComponent } from './TpIconlocalComponent';
