import { Component, Input, TemplateRef } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';

/**
 * 帮助提示组件
 *
 * 由被提示的文本和一个问号图标构成，鼠标悬浮在图标上可以看到对文本内容的解释说明
 *
 * <example-url>../tinyplus3demo/#/helptip/helptip-all</example-url>
 */
@Component({
    selector: 'tp-helptip',
    templateUrl: './helptip.html',
    styleUrls: ['./helptip.less'],
    // host: {
    //     '[class.tp-helptip-wrapper]': 'true'
    // }
})

export class TpHelptipComponent extends TiBaseComponent {
    /**
     * 待解释的的文本内容
     */
    @Input() label: string;
    /**
     * 提示说明文本，可为字符串/ng-template/component
     */
    @Input() iconTip: string | TemplateRef<any> | any;
    /**
     * 提示说明方向
     */
    @Input() iconTipPosition: string;
    /**
     * 提示说明的最大宽度
     *
     * 9.0.4 版本新增
     *
     * @default '276px'
     */
    @Input() iconTipMaxWidth: string;
}
