import { Component } from '@angular/core';

/**
 * 布局组件，提供整个页面内容区域的布局
 *
 * 内容容器
 *
 * <example-url>../tinyplus3demo/#/layout/layout-all</example-url>
 */
@Component({
    selector: 'tp-layout-content',
    template: '<ng-content></ng-content>',
    styleUrls: ['./layout-content.less'],
    host: {
        '[class.tp-layout-content]': 'true'
    }
})
export class TpLayoutContentComponent {
}
