import { Injectable } from '@angular/core';
import { TpBindingModule } from './TpBindingModule';
import { TiLocale } from '@cloud/tiny3';
import { TpGoandbackService, TpGoAndBackConfig } from '../goandback/TpGoandbackService';

/**
 * 绑定组件  binding，使用该服务时需要引入模块TpBindingModule，导入服务TpBindingSerive,通过调用TpBindingService.open(config)方法生成绑定内容
 *
 * <example-url>../tinyplus3demo/#/binding/binding-all</example-url>
 */
@Injectable({
    providedIn: TpBindingModule
})
export class TpBindingService extends TpGoandbackService {
    protected words: any = TiLocale.getLocaleWords().tpBinding;

    public open(config?: TpGoAndBackConfig): void {
        // binding组件-url无需添加params处理
        config.addUrlParams = false;
        super.open(config);
    }
}
