

import {
    Directive,
    OnDestroy,
    OnInit
} from '@angular/core';
import { TiTableColumns, TiTableComponent, Util } from '@cloud/tiny3';
import { CfUser } from '@cloud/cfdata';
import { CfUtil } from '../../cfutils/CfUtil'
import { Subscription } from 'rxjs';

/**
 * 表格记忆列切换指令
 *
 * 10.0.4 新增
 *
 *
 * <example-url>../tinyplus3demo/#/storage/storage-all</example-url>
 */
@Directive({
    selector: 'ti-table[tpStorageColumns]'
})
export class TpStorageColumnsDirective implements OnInit, OnDestroy {
    /**
     * @ignore
     */
    public static readonly GET_URL: string = './rest/silvan/rest/v1.0/setting/{userid}/{category}/update.json';
    /**
     * @ignore
     */
    public static readonly POST_URL: string = './rest/silvan/rest/v1.0/setting/{userid}/update.json';
    private tableCom: TiTableComponent;
    private httpService: any;
    private user: CfUser;
    private updateColumnsSubscription: Subscription;
    constructor(tableCom: TiTableComponent) {
        this.tableCom = tableCom;
    }
    ngOnInit(): void {
        if (this.shouldStorageColumnsToggle()) {
            this.httpService = CfUtil.getHttpService();
            CfUtil.getUser()
                .then((user: CfUser) => {
                    this.user = user
                    this.getColumns();
                    this.updateColumnsSubscription = this.tableCom.updateColumnsSubject.subscribe(() => {
                        this.postColumns();
                    });
                }).catch(error => {
                    console.log(error);
                });
        }
    }
    ngOnDestroy(): void {
        if (this.updateColumnsSubscription) {
            this.updateColumnsSubscription.unsubscribe();
        }
    }
    private getColumns(): void {
        if (!this.user && !this.httpService) {
            return;
        }
        const userid = this.user.userId;
        const storageId = this.tableCom.storageId;
        this.httpService.get({
            url: {
                s: TpStorageColumnsDirective.GET_URL,
                o: {
                    userid: userid,
                    category: storageId
                }
            }
        }).then((data: Array<any>) => {
            this.tableCom.columns.forEach((column: TiTableColumns, index: number) => {
                if (!Util.isNull(data[index])) {
                    column.show = data[index];
                };
            })
        }).catch(error => {
            console.log(error);
        });
    }
    private postColumns(): void {
        if (!this.user && !this.httpService) {
            return;
        }

        const columns = this.tableCom.columns.map((column: TiTableColumns): boolean => {
            return column.show;
        });
        const userid = this.user.userId;
        const storageId = this.tableCom.storageId;
        this.httpService.post({
            url: {
                s: TpStorageColumnsDirective.POST_URL,
                o: {
                    userid: userid
                }
            },
            params: {
                ategory: storageId,
                setting: JSON.stringify(columns),
                lastModifiedDateLong: new Date().getTime()
            }
        }).then((data: any) => {
            // 成功后不做处理
        }).catch((error) => {
            console.log(error);
        });
    }
    private shouldStorageColumnsToggle(): boolean {
        return !!(this.tableCom && this.tableCom.storageId);
    }
}
