import { Component, Input, SimpleChanges } from '@angular/core';
import { TiBaseComponent, TiLocale } from '@cloud/tiny3';

/**
 * 折叠文本
 *
 * 10.0.1 新增
 *
 * <example-url>../tinyplus3demo/#/foldtext/foldtext-all</example-url>
 */
@Component({
    selector: 'tp-foldtext',
    templateUrl: './foldtext.html',
    styleUrls: ['./foldtext.less']
})
export class TpFoldtextComponent extends TiBaseComponent {
    /**
     * 文本内容
     */
    @Input() texts: Array<string> = [];
    /**
     * 折叠时显示几行
     */
    @Input() line: number = 2;
    /**
     * @ignore
     * “查看全部” 词条
     */
    public showMore: string = TiLocale.getLocaleWords().tpFoldtext.showMore;
    /**
     * @ignore
     * “收起” 词条
     */
    public showLess: string = TiLocale.getLocaleWords().tpFoldtext.showLess;

    /**
     * @ignore
     * 显示文本内容的高度
     */
    public height: string;
    /**
     * @ignore
     * 是否折叠
     */
    public folded: boolean = true;
    // 表格中的默认font-size:12px, 默认line-height:1.5, 所以默认值取18px
    private lineHeight: string = '18px';

    // tslint:disable-next-line: use-life-cycle-interface
    ngAfterViewInit(): void {
        super.ngAfterViewInit();

        const CSSStyles: CSSStyleDeclaration = getComputedStyle(this.nativeElement);
        const lineHeight: string = CSSStyles.getPropertyValue('line-height');

        if (!Number.isNaN(parseFloat(lineHeight))) {
            this.lineHeight = lineHeight;
        }
        // 修改了页面的数据height，消除ExpressionChangedAfterItHasBeenCheckedError报错使用setTimeout
        setTimeout(() => {
            this.height = (this.line * parseFloat(this.lineHeight)) + 'px';
        }, 0);
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);

        if (changes['texts'] && !changes['texts'].firstChange || changes['line'] && !changes['line'].firstChange) {
            this.height = (this.line * parseFloat(this.lineHeight)) + 'px';
        }
    }
    /**
     * @ignore
     * 切换文本的点击事件
     */
    public onClick(event: Event): void {
        this.folded = !this.folded;
    }

}
