import { Pipe, PipeTransform } from '@angular/core';
import { TiLocale } from '@cloud/tiny3';
import { TpLocaleWords } from '../../locale/i18n';
import { formatDate } from '@angular/common';
/**
 * 日期格式化
 *
 * 默认按照局点配置格式
 *
 * <example-url>../tinyplus3demo/#/locale/locale-all</example-url>
 */
@Pipe({ name: 'tpDate', pure: false })
export class TpDatePipe implements PipeTransform {
  /**
   * 日期转换
   * @param value 日期
   * @param format 显示种类
   * @returns 转换结果
   */
  transform(value: string | number | Date, format: 'date'|'time'|'dateTime'|'dateTimeZone' = 'date'): string {
    // format = format ? format : 'date'; // 默认值是'date'
    const res: string = formatDate(value, (TiLocale.getLocaleWords() as TpLocaleWords).tiLocaleDate[format], TiLocale.EN_US);

    return res;
  }
}
