import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TiPopupModule, TiOutlineModule } from '@cloud/tiny3';
import { TpCopyComponent } from './TpCopyComponent';
import { TpToastComponent } from './TpToastComponent';

import { TpIconModule } from '../icon/TpIconModule';

@NgModule({
  imports: [
    CommonModule,
    TiPopupModule,
    TpIconModule,
    TiOutlineModule
  ],
  exports: [TpCopyComponent],
  declarations: [TpCopyComponent, TpToastComponent],
  entryComponents: [TpToastComponent]
})

export class TpCopyModule {}
export { TpCopyComponent } from './TpCopyComponent';
