import {
    ChangeDetectorRef,
    Component,
    ElementRef,
    EventEmitter,
    Input,
    Output,
    Renderer2,
    SimpleChanges,
    ViewChild,
    ViewChildren,
    QueryList
} from '@angular/core';
import { AbstractControl, FormControl, FormGroup, ValidationErrors, ValidatorFn } from '@angular/forms';
import {
    TiBrowser, TiDateComponent, TiDominatorComponent, TiDropComponent, TiDropsearchComponent,
    TiFormComponent, TiKeymap, TiLocale, TiLocaleFormat, TiValidators, Util
    // tslint:disable-next-line: no-implicit-dependencies
} from '@cloud/tiny3';

import { TpIconComponent } from '../icon/TpIconModule';

/**
 * tag类型, radio-默认单选， noValue-非正常tag，tag值为空，  checkbox-多选，map-键值tag, numRange-数字范围tag,
 * dateRange-日期范围tag。tag 的分类是根据键值的结果划分
 */
export type TpSearchboxTagType = 'radio' | 'noValue' | 'checkbox' | 'map' | 'numRange' | 'dateRange';
/**
 * 候选tag数据配置项
 */
export interface TpSearchboxItem {
    /**
     * 搜索字段，tag的键
     */
    field: string;
    /**
     * tag 键的显示值，实际结果是field
     */
    label: string;
    /**
     * 配置项可生产的tag类型
     */
    type?: TpSearchboxTagType;
    /**
     * tag 值的选择项数据
     */
    options: Array<TpSearchboxOption>;
    /**
     * 自动识别匹配正则
     * 10.0.2 新增
     */
    regexp?: RegExp;
    /**
     * radio 单选类型可设置, 设置为false时,单选属性可以多次选择
     */
    replace?: boolean;
    /**
     * 单选或多选值的选中项键值
     * 10.0.2 新增
     */
    optionValueKey?: string;
    /**
     * dateRange 类型日期显示和结果格式，dateRange时必选
     */
    format?: string;
    /**
     * numRange 最小值，类型为number
     * dateRange开始日期，类型为Date
     */
    start?: number | Date;
    /**
     * numRange 最大值，类型为number
     * dateRange起始日期，类型为Date
     */
    end?: number | Date;
    /**
     * numRange 可填最小值，用于校验
     * dateRange可选最小值，用于校验
     */
    min?: number | Date;
    /**
     * numRange 可填最大值，用于校验
     * dateRange可选最大值，用于校验
     */
    max?: number | Date;
    /**
     * 每个item对应的提示文本
     */
    placeholder?: string;
    [propName: string]: any;
}

export interface TpSearchboxOption {
    /**
     * 选项显示值
     */
    label: string;
    /**
     * 搜索目标字段，只有‘label’才需要
     */
    field?: string;
    /**
     * 控制map类型二级选项是否出现内置所有值，map类型需要
     */
    allValues?: boolean;
    /**
     * 控制map类型二级选项是否出现内置空值，map类型需要
     */
    emptyValue?: boolean;
    /**
     * map类型二级选项数据
     */
    options?: Array<any>;
    [propName: string]: any;
}
/**
 * 每个选中tag的类型
 */
export interface TpSearchboxTag {
    /**
     * 搜索目标搜字段
     */
    field: string;
    /**
     * tag键，field的显示值
     */
    label: string;
    /**
     * tag值
     */
    value: string;
    /**
     * 类型
     */
    type: TpSearchboxTagType;
    /**
     * 数字范围和日期范围tag 开始值
     */
    start?: number | string;
    /**
     * 数字范围和日期范围tag 结束值
     */
    end?: number | string;
    [propName: string]: any;
}
/**
 * search 事件回调参数的数据对象，对象的键对应field，对象的值是字符串数组，单选单个，多选多个
 */
export interface TpSearchboxFilter {
    [propName: string]: Array<String>;
}

/**
 * ip正则
 * 10.0.2 新增
 */
export const TP_REGEXP_IP: RegExp = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])(\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])){3}$/;
/**
 * UUID正则
 * 10.0.2 新增
 */
export const TP_REGEXP_UUID: RegExp = /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i;

/**
 * 综合搜索组件。
 * 用户通过该组件，选择出数据过滤需要的参数
 *
 * <example-url>../tinyplus3demo/#/searchbox/searchbox-all</example-url>
 */
@Component({
    selector: 'tp-searchbox',
    templateUrl: './searchbox.html',
    styleUrls: ['./searchbox.less'],
    host: {
        '(blur)': 'onBlur()',
        '(click)': 'onClick($event)'
    },
    providers: [TiFormComponent.getValueAccessor(TpSearchboxComponent)]
})
export class TpSearchboxComponent extends TiFormComponent {
    /**
     * @ignore
     * formControl 校验状态，通过了所有有效性检查
     */
    public static readonly VALID: string = 'VALID';
    /**
     * @ignore
     * formControl 校验状态，至少有一个有效性检查失败了
     */
    public static readonly INVALID: string = 'INVALID';

    /**
     * 设置默认的过滤范围字段
     */
    @Input() defaultField: string = '';
    /**
     * 没有生成tag时的占位提示文本
     *
     */
    @Input() emptyPlaceholder: string = '';

    /**
     * 输入框的宽度
     */
    @Input() inputWidth: string;

    /**
     * 是否显示帮助图标
     *
     */
    @Input() showHelp: boolean = true;
    /**
     * 是否显示设置defaultField图标
     */
    @Input() showSettings: boolean = false;
    /**
     * 源数据
     */
    @Input() items: Array<TpSearchboxItem> = [];
    /**
     * 设置组件最多显示几行，
     */
    @Input() maxLine: number = 2;

    /**
     * 下拉框最大高度，单位为px，
     *
     */
    @Input() panelMaxHeight: string = '999px';

    /**
     * 设置属性面板宽度
     */
    @Input() propPanelWidth: string = '200px';

    /**
     * 设置属性值面板宽度
     */
    @Input() valuePanelWidth: string = '200px';
    /**
     * 设置tag的最大宽度，解决tag太长，属性值
     */
    @Input() tagMaxWidth: string = '400px';

    /**
     * help 图标提示事件
     */
    @Output() readonly help: EventEmitter<any> = new EventEmitter<any>();
    /**
     * 清除事件
     */
    @Output() readonly clear: EventEmitter<any> = new EventEmitter<any>();
    /**
     * 搜索事件
     */
    @Output() readonly search: EventEmitter<TpSearchboxFilter> = new EventEmitter<TpSearchboxFilter>();
    /**
     * 值面板打开前事件,用于懒加载值选项
     */
    @Output() readonly beforeOpen: EventEmitter<TpSearchboxComponent> = new EventEmitter<TpSearchboxComponent>();
    /**
     * @ignore
     * 选中项后，输入值变化事件
     * 10.0.2 新增
     */
    @Output() readonly inputValueChange: EventEmitter<any> = new EventEmitter<any>();

    /**
     * 默认匹配字段发生改变事件
     * 10.1.2 新增
     */
    @Output() readonly defaultFieldChange: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     * 内部标签donimator组件
     */
    @ViewChild(TiDominatorComponent, { static: true }) dominatorCom: TiDominatorComponent;
    /**
     * @ignore
     * 属性面板
     */
    @ViewChild('propDrop', { static: true }) propDropCom: TiDropsearchComponent;
    /**
     * @ignore
     * 值面板
     */
    @ViewChild('valueDrop', { static: true }) valueDropCom: TiDropsearchComponent;
    /**
     * @ignore
     * 多选值面板
     */
    @ViewChild('valueMultiDrop', { static: false }) valueMultiDropCom: TiDropsearchComponent;
    /**
     * @ignore
     * 自定义面板 numRange / daterange
     */
    @ViewChild('customDrop', { static: false }) customDropCom: TiDropComponent;
    /**
     * @ignore
     * 设置面板
     */
    @ViewChild('settingsDrop', { static: false }) settingsDropCom: TiDropComponent;
    /**
     * @ignore
     * 日期范围开始日期
     */
    @ViewChild('dateRangeStart', { static: false }) dateRangeStartCom: TiDateComponent;
    /**
     * @ignore
     * 日期范围结束日期
     */
    @ViewChild('dateRangeEnd', { static: false }) dateRangeEndCom: TiDateComponent;
    /**
     * @ignore
     * 搜索输入框
     */
    @ViewChild('searchInput', { static: false }) searchInputRf: ElementRef;
    /**
     * @ignore
     * settings 设置图标 用于drop 设置dominatorElem
     */
    @ViewChild('settings', { static: false }) settingsCom: TpIconComponent;
    /**
     * @ignore
     * 右侧图标（help、setting、search）
     */
    @ViewChildren('rightIcon') rightIconsRef: QueryList<TpIconComponent>;
    /**
     * @ignore
     * 分割符号，默认是中文冒号
     */
    public tagSeperator: string = '：';
    /**
     * @ignore
     * 有选中标签后的placeholder
     */
    public inputPlaceholder: string = TiLocale.getLocaleWords().tpSearchbox.addPlaceholder;
    /**
     * @ignore
     * 属性选项
     */
    public propDropOptions: Array<any> = [];
    /**
     * @ignore
     * 值选项
     */
    public valueDropOptions: Array<any> = [];
    /**
     * @ignore
     * 输入框的值
     */
    public inputValue: string = '';
    /**
     * @ignore
     * 选中item, 编辑和选择中都会存在
     */
    public selectedItem: TpSearchboxItem;
    /**
     * @ignore
     * map类型标签,第一级选中的选项
     */
    public selectedOption: TpSearchboxOption;
    /**
     * @ignore
     * 正在编辑tag
     */
    public editingTag: TpSearchboxTag;

    /**
     * @ignore
     * 值面板dominator
     */
    public valueDropDominator: HTMLElement;
    /**
     * @ignore
     * 值面板dominator
     */
    public settingsDropDominatorElem: HTMLElement;
    /**
     * @ignore
     * 帮助图标提示内容
     */
    public helpTipContent: string = TiLocale.getLocaleWords().tpSearchbox.help;
    /**
     * @ignore
     * 清空图标提示内容
     */
    public clearTipContent: string = TiLocale.getLocaleWords().tpSearchbox.clearAll;
    /**
     * @ignore
     * 设置默认搜索项图标提示内容
     */
    public settingTipContent: string = TiLocale.getLocaleWords().tpSearchbox.switchText;
    /**
     * @ignore
     * 单选值面板选中项
     */
    public propDropSelected: TpSearchboxOption;
    /**
     * @ignore
     * 单选值面板选中项
     */
    public valueDropSelected: TpSearchboxOption;
    /**
     * @ignore
     * 多选值面板选中项
     */
    public valueDropSelecteds: Array<TpSearchboxOption> = [];
    /**
     * @ignore
     * 设置defaultField面板选中项
     */
    public settingsDropSelected: TpSearchboxOption;
    /**
     * @ignore
     * 设置defaultField面板选项
     */
    public settingDropOptions: Array<TpSearchboxOption> = [];

    /**
     * @ignore
     * drop 面板到触发元素的距离，编辑标签时，距离是6px,所以使用变量
     */
    public dominatorSpace: string = '7px';
    /**
     * 记录默认选中项旧值，用于判断两次选中是否是同一个值。如果是，则不触发defaultFieldChange事件。
     */
    private oldDefaultField: string = '';

    public dominatorPlaceholder: string = '';

    // 存储一致需要聚焦的元素
    private alwaysFocusableElems: Array<any> = [];
    // 范围tag 使用的特殊字符串'-'
    private rangeLine: string = '-';
    // 范围tag 使用的特殊字符串'≥'
    private rangeLarge: string = '≥';
    // 范围tag 使用的特殊字符串'≤'
    private rangeLess: string = '≤';

    // eslint-disable-next-line @typescript-eslint/no-parameter-properties
    constructor(hostRef: ElementRef, render: Renderer2, private changeDetectorRef: ChangeDetectorRef) {
        super(hostRef, render);
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngOnInit(): void {
        super.ngOnInit();
        this.init();
        // 非中文场景下，标签分割符号是英文冒号加空格。
        if (TiLocale.getLocaleWords().tiLocaleKey !== 'zh-CN') {
            this.tagSeperator = ': ';
        }
        this.oldDefaultField = this.defaultField;
        this.setPlaceholder(this.model);
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        if (changes['items'] && this.items && !changes['items'].firstChange) {
            this.init();
        }

        if (changes['showSettings'] && this.showSettings) {
            // 初始化设置按钮下拉面板中的选项。
            this.initSettingDropOptions();
        } else if (changes['showSettings'] && !this.showSettings) {
            this.settingsDropDominatorElem = null;
        }

        if (changes['emptyPlaceholder'] && !changes['emptyPlaceholder'].firstChange && this.emptyPlaceholder) {
            this.setPlaceholder(this.model);
        }
    }

    ngAfterViewChecked (): void {
        if (this.settingsCom && !this.settingsDropDominatorElem) {
            // 根据设置按钮dom，来设置下拉面板的宽度
            this.settingsDropDominatorElem = this.settingsCom.nativeElement;
            // ngAfterViewChecked是视图已经变化检测结束的钩子函数，如果此时再去改变视图上的接口会有报错.
            // 报错信息：(ExpressionChangedAfterItHasBeenCheckedError) 所以需要强制执行一次变化检测。
            this.changeDetectorRef.detectChanges();
        }
    }
    // tslint:disable-next-line: use-life-cycle-interface
    ngAfterViewInit(): void {
        super.ngAfterViewInit();
        // 值面板可能对齐输入框，也可能对齐标签
        this.valueDropDominator = this.searchInputRf.nativeElement;
        if (this.showSettings) {
            this.settingsDropDominatorElem = this.settingsCom.nativeElement;
        }

        this.alwaysFocusableElems.push(this.dominatorCom.nativeElement, this.searchInputRf.nativeElement,
            );
        this.rightIconsRef.forEach((item: TpIconComponent): void => {
            this.alwaysFocusableElems.push(item.nativeElement);
        });
        this.setFocusableElems(this.alwaysFocusableElems);

        //  因为改变了valueDropDominator ，所以强制刷新一次
        this.changeDetectorRef.detectChanges();
    }

    /**
     * @ignore
     * 设置默认field字段面板选中项
     */
    public onSettingDropSelect(option: TpSearchboxOption): void {
        this.defaultField = option.field;
        this.settingsDropCom.hide();
        // 如果最新的选中项和之前不一样，才会触发defaultFieldChange事件。
        if (this.oldDefaultField !== this.defaultField) {
            this.oldDefaultField = this.defaultField;
            this.defaultFieldChange.emit(option);
        }
    }
    /**
     * @ignore
     * tag发生改变（删减、新增）时触发。
     * TODO: 为什么只有在tag删除的时候，才会触发该函数
     */
    public onDominatorModelChange(): void {
        this.setPlaceholder(this.model);
    }

    private setPlaceholder(model: any): void {
        this.dominatorPlaceholder = model?.length > 0 ? this.inputPlaceholder : this.emptyPlaceholder;
    }

    // 输入框处理
    /**
     * @ignore
     * 输入点击事件
     */
    public onClickInput(event: Event): void {

        this.valueDropDominator = this.searchInputRf.nativeElement;
        // 键已经选中
        if (this.selectedItem) {
            // 如果没有可选项，不显示
            if (!this.selectedItem.options && (!this.selectedItem.type || this.selectedItem.type === 'radio')) {
                return;
            }
            this.setValueDropSearchResult(this.getValueDropOptions(this.selectedItem.field));
            this.showValueDrop();

        } else {
            this.propDropSearch(this.inputValue);
        }
    }
    // 属性面板根据输入框中的值搜索选项
    private propDropSearch(value: string): void {
        const searchResult: Array<TpSearchboxOption> = this.searchOptions(value, this.propDropOptions);
        if (searchResult.length > 0) {
            this.propDropCom.searchResult = searchResult;
            this.propDropCom.show();
        } else {
            this.propDropCom.hide();
        }
    }

    /**
     * @ignore
     * 输入框按键按下处理Backspace和Enter键
     */
    public onKeydownInput(event: KeyboardEvent): void {
        // tslint:disable-next-line: deprecation
        switch (event.keyCode) {
            case TiKeymap.KEY_BACKSPACE:
                this.responseBackspace();
                this.setPlaceholder(this.model);
                break;
            case TiKeymap.KEY_ENTER:
                this.responseEnter(event);
                break;
            default:
                break;
        }
    }

    /**
     * @ignore
     * 输入框值变化处理，
     * 输入值时，如果展开的是属性面板，搜索属性，如果是值面板，单选搜索选项，多选匹配选项
     */
    public onInputValueChange(value: string): void {
        // 值面板
        if (this.selectedItem) {
            // 有选中项后，输入值之间通知出去，用于值选项数据过多，自己处理搜索。
            this.inputValueChange.emit({
                value,
                item: this.selectedItem
            });
            // 单选搜索
            if (this.selectedItem.type === 'radio') {
                this.dealRadioItemSearch(value);
            // 多选只匹配
            } else if (this.selectedItem.type === 'checkbox') {
                this.matchCheckboxInput(value.trim());
                this.showValueDrop();
            }

        } else { // 属性面板
            //  ie11 初始化和placeholder改变会触发ngModelChange事件，所有此处屏蔽属性搜索
            if (TiBrowser.isIE()) {
                return;
            }
            this.propDropSearch(value);
        }
    }
    /**
     * @ignore
     * 输入框失去焦点
     *
     */
    public onBlurInput(): void {
        this.valueDropCom.hide();
        this.propDropCom.hide();
        this.valueMultiDropCom.hide();
    }
    /**
     * @ignore
     * 输入框聚焦
     */
    public onFocusInput(): void {
        if (this.selectedItem && this.selectedItem.type === 'radio') {
            this.dealRadioItemSearch(this.inputValue);
        }
    }

    // 整体
    /**
     * @ignore
     * 整体失去焦点
     */
    public onBlur(): void {
        this.hideAllDrops();
    }
    /**
     * @ignore
     * 删除单个tag
     */
    public onDelete(): void {
        // 删除一项后，输入框位置改变，防止drop对齐问题，隐藏所有drop
        this.hideAllDrops();
    }
    /**
     * @ignore
     * 整体点击
     */
    public onClick(event: Event): void {
        this.searchInputRf.nativeElement.focus();
        this.onClickInput(event);
    }

    /**
     * @ignore
     * 清除所有tag
     */
    public onClickClear(event: Event): void {
        event.stopPropagation(); // 防止事件冒泡到dominator后，dominator聚焦之后面板打开。
        // 有选中tag才清空，防止多触发ngmodelchange
        if (this.model.length > 0) {
            this.model = [];
        }
        this.selectedItem = undefined;
        this.inputValue = '';
        this.clear.emit();
        this.setPlaceholder(this.model);
    }
    /**
     * @ignore
     * 点击搜索
     */
    public onClickSearch(event: Event): void {
        event.stopPropagation();

        // 先把输入的内容生成tag
        if (this.inputValue) {
            this.generateTagByInputValue();
        }

        this.hideAllDrops();

        const filter: TpSearchboxFilter = this.getFilter();
        this.search.emit(filter);
    }
    /**
     * @ignore
     * 点击help图标
     */
    public onClickHelp(event: Event): void {
        event.stopPropagation();
        this.help.emit(event);
    }
    /**
     * @ignore
     * 点击设置默认搜索项图标
     */
    public onClickSetting(event: Event): void {
        event.stopPropagation();
        this.settingsDropCom.show();
    }
    /**
     * @ignore
     * 是否有label 类型，用于label 类型的特殊处理
     */
    public hasLabeltag(): boolean {

        const index: number = this.items.findIndex((item: TpSearchboxItem): boolean => {
            return item.type === 'noValue';
        });

        return index > -1;
    }

    // 两个tag点击切换，事件顺序 mousedown => blur => focus => click
    // tag-value 点击事件 因为要设置焦点，所以要绑定在mousedown上
    /**
     * @ignore
     * 是否有label 类型，用于label 类型的特殊处理
     */
    public onMousedownTagValue(event: Event): void {
        this.setFocusableElems(this.alwaysFocusableElems.concat(event.target as HTMLElement));
    }
    /**
     * @ignore
     */
    public onClickTagValue(event: Event, tag: TpSearchboxTag): void {
        if (!this.tagEditable(tag.type)) {
            return;
        }
        event.stopPropagation();
        this.valueDropDominator = event.target as HTMLElement;
        this.editingTag = tag;

        this.hideValueDrops();

        this.selectedItem = this.items.find((item: TpSearchboxItem): boolean => {
            return item.field === tag.field;
        });

        this.setValueDropSearchResult(this.getValueDropOptions(tag.field));
        // 多选需要匹配已经选中的选项
        if (this.selectedItem.type === 'checkbox') {
            this.setValueDropSelecteds();
        }

        // 展开面板前更新了面板的参数，所以需要强制变检一次
        this.changeDetectorRef.detectChanges();

        // 如果没有可选项，不显示
        if (!this.selectedItem.options && (!this.selectedItem.type || this.selectedItem.type === 'radio')) {
            return;
        }
        // 在输入框选择时，再点击已选中标签编辑，面板无法展开，
        // 原因当前mousedown事件回调函数在 输入框blur事件之前，先打开后关闭，
        // 所以这里要添加延迟
        setTimeout((): void => {
            this.showValueDrop();
        }, 0);

    }
    /**
     * @ignore
     * tag值聚焦
     */
    public onFocusTagValue(event: Event, tag: TpSearchboxTag): void {
        event.preventDefault();
        const elem: HTMLElement = event.target as HTMLElement;
        this.valueDropDominator = elem;
        // 可编辑类型才选中
        if (tag.type === 'radio' || tag.type === 'checkbox') {
            this.selectHTMLElementText(elem);
        }
    }
    /**
     * @ignore
     * tag值失去焦点
     */
    public onBlurTagValue(): void {
        // 非编辑状态，保持面板的dominator在input上
        this.valueDropDominator = this.searchInputRf.nativeElement;
        this.propDropCom.hide();
        this.valueDropCom.hide();
        this.valueMultiDropCom.hide();
        if (this.selectedItem && (this.selectedItem.type === 'radio' || this.selectedItem.type === 'checkbox' || this.selectedItem.type === 'map')) {
            this.selectedItem = undefined;
            this.editingTag = undefined;
        }
    }

    /**
     * @ignore
     * tag 编辑输入框keydown事件，处理编辑状态下 enter 键操作
     */
    public onKeydownTagInput(event: KeyboardEvent): void {
        // tslint:disable-next-line: deprecation
        if (event.keyCode === TiKeymap.KEY_ENTER) {
            const tagValue: string = (event.target as HTMLElement).textContent;
            if (this.isTagExist(this.selectedItem, tagValue)) {
                return;
            }
            this.generateTag(this.selectedItem, tagValue);
            this.emitChange();
            this.selectedItem = undefined;
            this.editingTag = undefined;
            this.hideValueDrops();
        }
    }

    /**
     * @ignore
     * 标签编辑时只有多选需要处理匹配
     */
    public onTagInput(event: Event): void {
        if (this.selectedItem.type === 'checkbox') {
            const value: string = (event.target as HTMLElement).textContent.trim();
            this.matchCheckboxInput(value);
        }
    }

    /**
     * @ignore
     * 是否可以编辑
     */
    public tagEditable(type: TpSearchboxTagType): boolean {
        // map 类型标签、日期范围和数字范围不能编辑
        return type === 'radio' || type === 'checkbox';
    }
    /**
     * @ignore
     *  属性面板选中回调
     */
    public onPorpDropSelect(option: TpSearchboxOption): void {
        // label类型tag 只在属性面板中选择一次
        if (option.type === 'noValue') {
            this.generateTagByPropDrop(option);
            this.propDropCom.hide();

            return;
        }

        this.selectedItem = this.items.find((item: TpSearchboxItem): boolean => {
            return item.field === option.field;
        });
        // 有beforeOpen懒加载，面板展开交给用户
        if (this.beforeOpen.observers.length > 0) {
            this.beforeOpen.emit(this);

            return;
        }

        this.dominatorPlaceholder = option?.placeholder || (this.model.length > 0 ? this.inputPlaceholder : this.emptyPlaceholder);

        // 如果没有可选项，则不显示值面板。
        if (!this.selectedItem || !this.selectedItem.options && (!this.selectedItem.type || this.selectedItem.type === 'radio')) {
            // 先触发动态变化检测，再清除选中属性值（属性面板不允许有选中状态）
            this.changeDetectorRef.detectChanges();
            this.clearPropDropSelected();

            return;
        }

        const options: Array<TpSearchboxOption> = this.getValueDropOptions(option.field);
        this.valueDropOptions = options;
        this.setValueDropSearchResult(options);

        if (this.selectedItem.type === 'checkbox') {
            this.setValueDropSelecteds();
        }

         // 面板展开前清空输入内容
         this.inputValue = '';

        // 展开面板前更新了面板的参数，所以需要强制变检一次
        this.changeDetectorRef.detectChanges();
        this.showValueDrop();
    }

    /**
     * 暴露外面的打开值面板接口
     */
    public open(options: Array<TpSearchboxOption>): void {
        this.setCustomValueDropSearchResult(options);
        this.showValueDrop();
    }
    /**
     * 获取但前选中数据项
     */
    public getSelectedItem(): TpSearchboxItem {
        return this.selectedItem;
    }

    /**
     * @ignore
     * 单选值面板选中，(包括map类型)
     */
    public onValueDropSelect(selected: TpSearchboxOption): void {
        // 复选时，点击确定按钮结束选择
        if (!this.selectedItem || !selected ) {
            return;
        }
        // 处理map类型，map类型需要选择两次
        if (this.selectedItem.type === 'map') {
            this.mapItemSelect(selected);
            return;
        }

        this.generateTagByValuePanel();
        this.clearState();
    }

    /**
     * @ignore
     * 多选底部确定按钮
     */
    public onClickValueDropOK(event: Event): void {
        //  阻止按钮获取焦点，输入框失去焦点
        event.preventDefault();
        this.generateTagByValuePanel();
        this.clearState();
    }

    /**
     * @ignore
     * 多选底部取消按钮
     */
    public onClickValueDropCancel(): void {
        this.valueMultiDropCom.hide();
    }
    /**
     * @ignore
     * 数字范围
     */
    public onClickCustomDropOK(): void {

        if (!this.selectedItem) {
            return;
        }
        if (this.selectedItem.type === 'numRange') {
            this.generateNumRangetag();
        } else if (this.selectedItem.type === 'dateRange') {
            this.generateDateRangeTag();
        }

    }

    /**
     * @ignore
     * 自定义面板取消按钮回调
     */
    public onClickCustomDropCancel(): void {
        this.hideCustomDrop();
        if (this.editingTag) {
            this.selectedItem = undefined;
            this.editingTag = undefined;
        }
    }
    /**
     * @ignore
     * numRange 和 dateRange 面板的宽度需要固定值
     */
    public customDropWidth(): string {
        if (!this.selectedItem) {
            return;
        } else if (this.selectedItem.type === 'numRange') {
            return '200px';
        } else if (this.selectedItem.type === 'dateRange') {
            // 根据最宽小语种pt_BR词条
            return '300px';
        }
    }

    private init(): void {
        this.initItems();
        this.initPropDropOptions();
    }
    private initItems(): void {
        // 初始化 allValues/ emptyValue
        this.items.forEach((item: TpSearchboxItem): void => {
            // items 中不写type, 默认单选'radio'
            if (Util.isUndefined(item.type)) {
                item.type = 'radio';
            } else if (item.type === 'map' && Array.isArray(item.options)) {
                item.options.forEach((option: TpSearchboxOption): void => {
                    // 所有值项
                    if (option.allValues !== false && Array.isArray(option.options)) {
                        const allValues: string = TiLocale.getLocaleWords().tpSearchbox.allValues;
                        const allValueOption: TpSearchboxOption = option.options.find((currentOption: TpSearchboxOption): boolean => {
                            return currentOption.label === allValues;
                        });

                        if (!allValueOption) {
                            option.options.unshift({
                                label: allValues
                            });
                        }
                    }
                    // 空值项 emptyValue
                    if (option.emptyValue !== false && Array.isArray(option.options)) {
                        const emptyValue: string = TiLocale.getLocaleWords().tpSearchbox.emptyValue;
                        const emptyValueOption: TpSearchboxOption = option.options.find((currentOption: TpSearchboxOption): boolean => {
                            return currentOption.label === emptyValue;
                        });
                        if (!emptyValueOption) {
                            option.options.unshift({
                                label: emptyValue
                            });
                        }
                    }
                });
                // 数字范围
            } else if (item.type === 'numRange') {

                const start: FormControl = new FormControl(item.start,
                    [TiValidators.rangeValue(item.min as number, item.max as number),
                    this.lessMax('end')]);
                const end: FormControl = new FormControl(item.end,
                    [TiValidators.rangeValue(item.min as number, item.max as number),
                    this.moreMin('start')]);
                item.formControls = new FormGroup({
                    start,
                    end
                });
                // 开始值的校验状态改变后，更新一下结束值的校验状态
                start.statusChanges.subscribe((status: string): void => {
                    if (status === TpSearchboxComponent.VALID && end.status === TpSearchboxComponent.INVALID) {
                        end.updateValueAndValidity({
                            onlySelf: true,
                            emitEvent: false
                        });
                    }
                });

                // 结束值的校验状态改变后，更新一下开始值的校验状态
                end.statusChanges.subscribe((status: string): void => {
                    if (status === TpSearchboxComponent.VALID && start.status === TpSearchboxComponent.INVALID) {
                        start.updateValueAndValidity({
                            onlySelf: true,
                            emitEvent: false
                        });
                    }
                });
                // 日期范围
            } else if (item.type === 'dateRange') {
                // tslint:disable-next-line: no-null-keyword
                const startDate: Date = this.isValidDate(new Date(item.start)) ? new Date(item.start) : null;
                // tslint:disable-next-line: no-null-keyword
                const endDate: Date = this.isValidDate(new Date(item.end)) ? new Date(item.end) : null;
                const start: FormControl = new FormControl(startDate);
                const end: FormControl = new FormControl(endDate);
                item.formControls = new FormGroup({
                    start,
                    end
                });

                // 开始日期的max是结束日期的选中值，如果结束日期没有选择，使用item中设置的max
                item.startMax = endDate ? endDate : item.max;
                // 结束日期的min是开始日期的选中值，如果开始日期没有选择，使用item中设置的min
                item.endMin = startDate ? startDate : item.min;
                start.valueChanges.subscribe((value: Date): void => {
                    // tslint:disable-next-line: prefer-conditional-expression
                    if (this.isValidDate(value)) {
                        item.endMin = value;
                    } else if (this.isValidDate(item.min as Date)) {
                        item.endMin = item.min;
                    } else {
                        // 没有设置 min,开始日期选中又清除后，需要设置一个min的缺省值
                        item.endMin = undefined;
                    }
                });
                end.valueChanges.subscribe((value: Date): void => {
                    // tslint:disable-next-line: prefer-conditional-expression
                    if (this.isValidDate(value)) {
                        item.startMax = value;
                    } else if (this.isValidDate(item.max as Date)) {
                        item.startMax = item.max;
                    } else {
                        // 没有设置 max, 结束日期选中又清除后，需要设置一个max的缺省值
                        item.startMax = undefined;
                    }
                });
            }
        });
    }
    // Invalid Date判断
    // tslint:disable-next-line: prefer-function-over-method
    private isValidDate(date: Date | string): boolean {
        return Object.prototype.toString.call(date) === '[object Date]' && String(date) !== 'Invalid Date';
    }

    // 自定义校验规则小于最大值
    // 静态方法返回箭头函数生成环境打包错误，所以使用
    // tslint:disable-next-line: prefer-function-over-method
    private lessMax(field: string): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            if (control.parent && control.parent.get(field)) {
                const end: number = control.parent.get(field).value;
                // 开始和结束值都存在时，判断开始可结束值的大小
                if (end && control.value && control.value > end) {
                    return {
                        minValue: {
                            actualValue: control.value,
                            tiErrorMessage: TiLocale.getLocaleWords().tpSearchbox.rangeMinErr
                        }
                    };
                }
            }

            // tslint:disable-next-line: no-null-keyword
            return null;
        };
    }
    // 自定义校验规则，大于最小值
    // tslint:disable-next-line: prefer-function-over-method
    private moreMin(field: string): ValidatorFn {
        return (control: AbstractControl): ValidationErrors | null => {
            if (control.parent && control.parent.get(field)) {
                const start: number = control.parent.get(field).value;
                // 开始和结束值都存在时，判断开始可结束值的大小
                if (start && control.value && control.value < start) {
                    return {
                        minValue: {
                            actualValue: control.value,
                            tiErrorMessage: TiLocale.getLocaleWords().tpSearchbox.rangeMaxErr
                        }
                    };
                }
            }

            // tslint:disable-next-line: no-null-keyword
            return null;
        };
    }

    // 初始化属性面板选项
    private initPropDropOptions(): void {
        const options: Array<TpSearchboxOption> = [];
        this.items.forEach((item: TpSearchboxItem): void => {
            const option: TpSearchboxOption = {
                field: item.field,
                label: item.label,
                type: item.type,
                placeholder: item?.placeholder
            };
            // label
            if (item.type === 'noValue') {
                option['children'] = item.children.map((childOption: TpSearchboxOption): TpSearchboxOption => {
                    childOption.type = 'noValue';

                    return childOption;
                });
            }
            options.push(option);
        });

        this.propDropOptions = options;
    }
    private initSettingDropOptions(): void {
        // 只有noValue类型不能设置为默认搜索项
        const options: Array<TpSearchboxOption> = this.items.filter((item: TpSearchboxItem): boolean => {
            return item.type !== 'noValue';
        })
            .map((item: TpSearchboxItem): TpSearchboxOption => {
                const option: TpSearchboxOption = {
                    label: item.label,
                    field: item.field
                };
                // 如果设置了defaultField，面板中高亮显示
                if (this.defaultField === option.field) {
                    this.settingsDropSelected = option;
                }

                return option;
            });
        // 使用分组来显示面板标题
        this.settingDropOptions = [{
            label: this.settingTipContent,
            children: options
        }];
    }

    private matchCheckboxInput(value: string): void {
        const valueArray: Array<string> = value.split('|')
            .map((span: string): string => {
                return span.trim();
            });
        this.valueDropSelecteds = [];
        this.selectedItem.options.forEach((option: TpSearchboxOption): void => {
            if (valueArray.includes(option.label)) {
                this.valueDropSelecteds.push(option);
            }
        });
    }

    private dealRadioItemSearch(value: string): void {
        const searchResult: Array<TpSearchboxOption> = this.searchOptions(value, this.valueDropOptions);
        if (searchResult.length > 0) {
            this.setValueDropSearchResult(searchResult);
            this.showValueDrop();
        } else {
            this.valueDropCom.hide();
        }
    }

    // 设置值面板的选项
    private setValueDropSearchResult(options: Array<TpSearchboxOption>): void {
        if (this.selectedItem.type === 'checkbox') {
            this.valueMultiDropCom.searchResult = options;
        } else if (this.selectedItem.type === 'radio' || this.selectedItem.type === 'map') {
            this.valueDropCom.searchResult = options;
        }
    }
    // 懒加载时，设置值面板选项
    private setCustomValueDropSearchResult(options: Array<TpSearchboxOption>): void {
        if (this.selectedItem.type === 'checkbox') {
            this.selectedItem.options = options;
            this.valueMultiDropCom.searchResult = options;
        } else if (this.selectedItem.type === 'radio') {
            this.selectedItem.options = options;
            this.valueDropCom.searchResult = options;
        } else if (this.selectedItem.type === 'map') {
            this.selectedItem.options = options;
            this.valueDropCom.searchResult = [{
                label: TiLocale.getLocaleWords().tpSearchbox.tagKey,
                children: options
            }];

        }
        this.changeDetectorRef.detectChanges();
    }

    // 根据输入内容生成tag
    private generateTagByInputValue(): void {
        // 有选中项且可编辑
        if (this.selectedItem) {
            if (this.tagEditable(this.selectedItem.type) || this.selectedItem.type === 'map') {
                if (this.isTagExist(this.selectedItem, this.inputValue)) {
                    return;
                }
                this.generateTag(this.selectedItem, this.inputValue);
                this.emitChange();
                this.selectedItem = undefined;
                this.inputValue = '';
                this.hideValueDrops();
            }

            return;
        }

        // 没有选中项，按默认值处理
        if (!this.defaultField || this.inputValue === '') {
            return;
        }

        // 属性面板展开，会触发dropsearch的快捷键，所以先关闭
        this.propDropCom.hide();
        // 输入字符串之间可能使用中英文','分割
        const values: Array<string> = this.inputValue.split(/[，,]/)
            .map((value: string) => {
                return value.trim();
            })
            .filter((value: string) => {
                return value !== '';
            });
        if (values.length === 0) {
            return;
        }
        // 因为要多次生成tag, 会多次更新model,
        // 用此变量标记在最后一次生成更新model后发射ngModelChange事件
        let modelChanged: boolean = false;
        // 只支持每次输入都是同一类型的，所以多个值用第一个匹配，找到匹配项
        const matchedItem: TpSearchboxItem = this.items.find((item: TpSearchboxItem): boolean => {
            return item.regexp && item.regexp.test(values[0]);
        });

        if (matchedItem) {
            values.forEach((value: string) => {
                if (!this.isTagExist(matchedItem, value)) {
                    modelChanged = true;
                    this.generateTag(matchedItem, value);
                }
            });
        } else if (this.defaultField) {
            const defaultItem = this.items.find((item: TpSearchboxItem): boolean => {
                return this.defaultField === item.field;
            });
            // 默认项生成tag,不支持逗号分隔，所以用inputValue整体
            if (!this.isTagExist(defaultItem, this.inputValue)) {
                modelChanged = true;
                this.generateTag(defaultItem, this.inputValue);
            }
        }

        this.inputValue = '';
        if (modelChanged) {
            this.emitChange();
        }
    }

    // 属性面板没有选中项,所以要清除处理。如果不进行重置，则会出现选中样式~
    private clearPropDropSelected(): void {
        this.propDropSelected = undefined;
    }

    private responseEnter(event: Event): void {
        this.generateTagByInputValue();
    }
    private responseBackspace(): void {
        if (this.inputValue) {
            return;
        }
        // 处理map类型标签tag
        if (this.selectedOption) {
            this.selectedOption = undefined;
            this.valueDropCom.hide();
            const searchResult: Array<TpSearchboxOption> = this.getValueDropOptions(this.selectedItem.field);
            this.setValueDropSearchResult(searchResult);

            // 展开面板前更新了面板的参数，所以需要强制变检一次
            this.changeDetectorRef.detectChanges();

            this.showValueDrop();

        } else if (this.selectedItem) {
            // 处理有选中属性

            this.hideValueDrops();

            this.selectedItem = undefined;

            // 展开面板前更新了面板的参数，所以需要强制变检一次
            this.changeDetectorRef.detectChanges();
            this.propDropCom.show();
        } else if (this.model.length > 0) {
            // 处理已经选中的标签
            this.propDropCom.hide();

            // 清除items 中的选中项
            this.model.pop();
            // change事件
            this.emitChange();
        }

    }

    // 展示值面板封装单选和多选
    private showValueDrop(): void {
        // 当点击选中的tag值时，dominatorSpace 需要改变，
        // 5px = （22 -16）/2 +2 ; 22-tag高度，16-文字高度，2-上下两边，2-tag和组件边框之间的距离
        this.dominatorSpace = this.editingTag ? '10px' : '7px';
        // 展开面板前更新了面板的参数，所以需要强制变检一次
        this.changeDetectorRef.detectChanges();
        this.clearPropDropSelected();

        switch (this.selectedItem.type) {
            case 'radio':
                this.valueDropCom.show();
                break;
            case 'map':
                this.valueDropCom.show();
                break;
            case 'checkbox':
                this.valueMultiDropCom.show();
                break;
            case 'numRange':
                this.customDropCom.show();
                this.setNumRangeDropFocusableElems();
                break;
            case 'dateRange':
                this.customDropCom.show();
                this.setDateRangeDropFocusableElems();
                break;
            default:
                break;
        }
    }

    private hideAllDrops(): void {
        this.propDropCom.hide();
        this.hideValueDrops();
        if (this.showSettings) {
            this.settingsDropCom.hide();
        }
    }
    private hideValueDrops(): void {
        this.valueDropCom.hide();
        this.valueMultiDropCom.hide();
        this.customDropCom.hide();
    }

    private hideCustomDrop(): void {
        // 自定义面板隐藏前，让dominator重新聚焦，防止整体失焦
        this.dominatorCom.focus();
        this.customDropCom.hide();
    }
    // 根据关键词(忽略大小写)过滤options
    // tslint:disable-next-line: prefer-function-over-method
    private searchOptions(keyword: string, options: Array<TpSearchboxOption>): Array<TpSearchboxOption> {
        const keywordUpper: string = keyword.toLocaleUpperCase();

        return options.filter((option: TpSearchboxOption): boolean => {
            const labelUpper: string = option.label.toLocaleUpperCase();

            return labelUpper.indexOf(keywordUpper) !== -1;
        });
    }

    // map 类型
    private mapItemSelect(option: TpSearchboxOption): void {
        if (this.selectedOption) {
            const field: string = this.selectedItem.field;
            const label: string = this.selectedItem.label;
            const value: string = `${this.selectedOption.label}=${option.label}`;

            const newTag: TpSearchboxTag = {
                field,
                label,
                value,
                type: 'map'
            };

            if (!this.model) {
                this.model = [newTag];

                return;
            }

            const index: number = this.model.findIndex((tag: TpSearchboxTag): boolean => {
                return tag.field === field && tag.value === value;
            });

            // 标签编辑时，是替换
            if (this.editingTag) {
                this.editingTag = newTag;
                this.model.splice(index, 1, newTag);
            } else if (index === -1) {
                this.model.push(newTag);
            }
            this.emitChange();
            this.selectedItem = undefined;
            this.selectedOption = undefined;
            this.editingTag = undefined;

            this.valueDropCom.hide();
        } else {
            this.selectedOption = option;

            if (Array.isArray(this.selectedOption.options)) {
                const children: Array<TpSearchboxOption> = this.selectedOption.options;
                this.valueDropCom.searchResult = [{
                    label: TiLocale.getLocaleWords().tpSearchbox.tagValue,
                    children
                }];

            } else {
                this.valueDropCom.searchResult = [];
            }
            this.changeDetectorRef.detectChanges();
            // 点击选项后默认会关闭面板，所以延迟后再次打开
            setTimeout((): void => {
                this.showValueDrop();
            }, 0);
        }
    }

    private setValueDropSelecteds(): void {
        const selectedTag: TpSearchboxTag = this.model.find((tag: TpSearchboxTag): boolean => {
            return this.selectedItem.field === tag.field;
        });
        if (selectedTag) {
            this.matchCheckboxInput(selectedTag.value);
        }
    }
    // 清理临时状态
    private clearState(): void {
        this.editingTag = undefined;
        this.selectedItem = undefined;
        this.inputValue = '';
        this.valueDropSelecteds = [];
        this.valueDropSelected = undefined;
        this.hideValueDrops();
    }

    private generateNumRangetag(): void {
        const values: any = this.selectedItem.formControls.value;
        if (this.selectedItem.formControls.status === TpSearchboxComponent.INVALID || (!values.start && !values.end)) {
            return;
        }
        let tagValue: string;
        if (values.start && values.end) {
            tagValue = `${values.start}${this.rangeLine}${values.end}`;
        } else if (values.start) {
            tagValue = this.rangeLarge + values.start;
        } else if (values.end) {
            tagValue = this.rangeLess + values.end;
        }
        // 数字范围可能有单位
        let label: string = this.selectedItem.label;
        if (this.selectedItem.unit) {
            label = `${label}(${this.selectedItem.unit})`;
        }
        const newTag: TpSearchboxTag = {
            field: this.selectedItem.field,
            label,
            type: 'numRange',
            start: values.start,
            end: values.end,
            value: tagValue
        };

        if (!this.model) {
            this.model = [newTag];

            return;
        }

        const tagIndex: number = this.model.findIndex((tag: TpSearchboxTag): boolean => {
            return tag.field === this.selectedItem.field;
        });

        if (tagIndex === -1) {
            this.model.push(newTag);
        } else {
            this.model.splice(tagIndex, 1, newTag);
        }
        this.afterGenerateTag();
    }

    private generateDateRangeTag(): void {
        const values: any = this.selectedItem.formControls.value;
        if (!values.start && !values.end) {
            return;
        }
        let tagValue: string = '';
        let start: string;
        let end: string;
        if (values.start && values.end) {
            tagValue = `${this.formatDate2String(values.start as Date)}${this.rangeLine}${this.formatDate2String(values.end as Date)}`;
            start = values.start.toISOString();
            end = values.end.toISOString();
        } else if (values.start) {
            tagValue = this.rangeLarge + this.formatDate2String(values.start as Date);
            start = values.start.toISOString();
        } else if (values.end) {
            tagValue = this.rangeLess + this.formatDate2String(values.end as Date);
            end = values.end.toISOString();
        }
        const newTag: TpSearchboxTag = {
            field: this.selectedItem.field,
            label: this.selectedItem.label,
            start,
            end,
            type: 'dateRange',
            value: tagValue
        };

        if (!this.model) {
            this.model = [newTag];

            return;
        }

        const tagIndex: number = this.model.findIndex((curTag: TpSearchboxTag): boolean => {
            return curTag.field === this.selectedItem.field;
        });

        if (tagIndex === -1) {
            this.model.push(newTag);
        } else {
            this.model.splice(tagIndex, 1, newTag);
        }
        this.afterGenerateTag();
    }

    private afterGenerateTag(): void {
        this.emitChange();
        this.hideCustomDrop();
        this.selectedItem = undefined;
    }

    private setNumRangeDropFocusableElems(): void {
        const inputs: NodeList = this.customDropCom.nativeElement.querySelectorAll('[tiText]');
        const inputElemsArray: Array<any> = [].slice.call(inputs);
        this.setFocusableElems(this.alwaysFocusableElems.concat(inputElemsArray, this.getButtons(), this.customDropCom.nativeElement));
    }

    private setDateRangeDropFocusableElems(): void {
        // 日期范围面板中的日期组件，存在焦点问题，
        const elemsNodeList: Array<any> = this.alwaysFocusableElems.concat(
            this.customDropCom.nativeElement,
            this.dateRangeStartCom.getFocusableElems(),
            this.dateRangeEndCom.getFocusableElems(),
            this.getButtons()
        );
        this.setFocusableElems(elemsNodeList);
        this.changeDetectorRef.detectChanges();
        this.changeDetectorRef.markForCheck();
    }

    private getButtons(): Array<any> {
        const buttons: NodeList = this.customDropCom.nativeElement.querySelectorAll('[tiButton]');

        return [].slice.call(buttons);
    }
    // 日期对象转字符串
    private formatDate2String(date: Date): string {
        if (!this.isValidDate(date)) {
            return '';
        } else {
            return TiLocaleFormat.formatDate(date, this.selectedItem.format);
        }
    }

    private generateTagByValuePanel(): void {
        let tagValue: string = '';
        // 单选复选
        if (this.selectedItem.type === 'checkbox') {
             tagValue = this.valueDropSelecteds.reduce((acc: string, cur: TpSearchboxOption, index: number): string => {
                return index === 0 ? cur.label : `${acc} | ${cur.label}`;
            }, '');
        } else {
            tagValue = this.valueDropSelected.label;
        }

        if (this.isTagExist(this.selectedItem, tagValue)) {
            return;
        }
        this.generateTag(this.selectedItem, tagValue);
        // change 事件
        this.emitChange();
    }
    // 当前要生成的tag是否已经存在
    private isTagExist(selectedItem: TpSearchboxItem, tagValue: string): boolean {
        // 新选中的tag是否已经存在
        const selectedTagIndex: number = this.model.findIndex((tag: TpSearchboxTag): boolean => {
            return tag.field === selectedItem.field && tag.value === tagValue;
        });

        return selectedTagIndex !== -1;
    }

    // label 类型的标签，只选择一次
    private generateTagByPropDrop(option: any): void {
        const newTag: TpSearchboxTag = {
            field: option.field,
            label: option.label,
            value: '',
            type: 'noValue'
        };
        if (!this.model) {
            this.model = [newTag];

            return;
        }
        // 只有一个label类型的tag
        const index: number = this.model.findIndex((tag: TpSearchboxTag): boolean => {
            return tag.type === 'noValue';
        });
        // 存在标签时替换，不存在时添加到最后
        if (index > 0) {
            this.model.splice(index, 1, newTag);
        } else {
            this.model.push(newTag);
        }
        this.emitChange();

    }

    // 生成tag
    private generateTag(selectedItem: TpSearchboxItem, tagValue: string): void {

        const newTag: TpSearchboxTag = {
            field: selectedItem.field,
            label: selectedItem.label,
            value: tagValue,
            type: selectedItem.type
        };
        // 如果选中field中设置了optionValueKey，给生成tag 添加额外的key, 单选为字符串，复选为字符串数组
        if(selectedItem.optionValueKey){
            this.addOtherOptionKey(newTag, selectedItem.optionValueKey);
        }

        this.updateModel(newTag, selectedItem);

    }
    private addOtherOptionKey(tag: TpSearchboxTag, valueKey: string): void {
        // 只对单选和复选做处理
        if (this.selectedItem.type === 'checkbox' && this.valueDropSelecteds) {
            this.valueDropSelecteds.forEach((option: TpSearchboxOption) => {
                if (Array.isArray(tag[valueKey])) {
                    tag[valueKey].push(option[valueKey])
                } else {
                    tag[valueKey] = [option[valueKey]];
                }
            });

        } else if (this.selectedItem.type === 'radio' && this.valueDropSelected) {
            tag[valueKey] = this.valueDropSelected[valueKey];
        }
    }

    private updateModel(newTag: TpSearchboxTag, selectedItem: TpSearchboxItem): void {

        if (!this.model) {
            this.model = [newTag];

            return;
        }

        // 编辑模式
        if (this.editingTag) {
            const editingTagIndex: number = this.model.findIndex((tag: TpSearchboxTag): boolean => {
                return tag.field === selectedItem.field && tag.value === this.editingTag.value;
            });
            this.model.splice(editingTagIndex, 1, newTag);
        } else {
            // tags中相同field
            const fieldTagIndex: number = this.model.findIndex((tag: TpSearchboxTag): boolean => {
                return tag.field === selectedItem.field;
            });
            // radio 类型可以设置是否替换，map默认不可替换,
            if (fieldTagIndex === -1 || (selectedItem.type === 'radio' && selectedItem.replace === false) || selectedItem.type === 'map') {
                this.model.push(newTag);
            } else {
                this.model.splice(fieldTagIndex, 1, newTag);
            }
        }

    }
    private emitChange(): void {
        this.model = this.model.concat();
        this.setPlaceholder(this.model);
    }
    private getValueDropOptions(field: string): Array<any> {

        let options: Array<TpSearchboxOption> = this.selectedItem && Array.isArray(this.selectedItem.options) ?
            this.selectedItem.options : [];
        // map类型，利用分组标题显示面板标题
        if (this.selectedItem.type === 'map') {
            // 如果已经选中标签的键
            if (this.selectedOption) {
                options = [
                    {
                        label: TiLocale.getLocaleWords().tpSearchbox.tagValue,
                        children: this.selectedOption.options
                    }
                ];
            } else {
                options = [
                    {
                        label: TiLocale.getLocaleWords().tpSearchbox.tagKey,
                        children: this.selectedItem.options
                    }
                ];
            }
        }

        return options;
    }

    // 生成结果 filter对象
    private getFilter(): TpSearchboxFilter {
        const filter: TpSearchboxFilter = {};
        this.model.forEach((tag: TpSearchboxTag): void => {
            // 已经存在，合并一起
            if (Array.isArray(filter[tag.field])) {
                filter[tag.field] = filter[tag.field].concat(tag.value.split('|')
                    .map((value: string): string => {
                        return value.trim();
                    }));
            } else {
                filter[tag.field] = tag.value.split('|')
                    .map((value: string): string => {
                        return value.trim();
                    });
            }
        });

        return filter;
    }
    // 选中一段html，三种浏览器测试正常
    // tslint:disable-next-line: prefer-function-over-method
    private selectHTMLElementText(elem: HTMLElement): void {
        const range: Range = document.createRange();
        range.selectNodeContents(elem);
        const selection: Selection = window.getSelection();
        selection.removeAllRanges();
        selection.addRange(range);
    }
    // tslint:disable-next-line: max-file-line-count
}
