import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TiLocale, Util, TiBaseComponent } from '@cloud/tiny3';
import { TpFrozenComponent } from '../frozen/TpFrozenModule';
/**
 * 冻结页面组件
 *
 * <example-url>../tinyplus3demo/#/frozenpage/frozenpage-all</example-url>
 */
@Component({
    selector: 'tp-frozenpage',
    templateUrl: './frozenpage.html',
    styleUrls: ['./frozenpage.less']
})

export class TpFrozenpageComponent extends TiBaseComponent {
    /**
     * 服务名称
     */
    @Input() serviceName: string;
    /**
     * 触发返回服务列表事件
     */
    @Output() readonly backServiceList: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     */
    public backService: string;
    /**
     * @ignore
     */
    public content: string = '';

    ngOnInit(): void {
        super.ngOnInit();
        this.backService = Util.formatEntry(TiLocale.getLocaleWords().tpFrozenpage.back_service, [this.serviceName]);
        TpFrozenComponent.queryFrozenInfo()
            .then((response: any) => {
                this.content = TpFrozenComponent.getMsgContent(response, 'POP');
            }, () => { });
    }

    /**
     * @ignore
     */
    public onClick(): void {
        this.backServiceList.emit();
    }
}
