import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
/**
 * <example-url>../tinyplus3demo/#/purchaselist/purchaselist-all</example-url>
 */
@Component({
    selector: 'tp-purchaseheader-item',
    templateUrl: './purchaseitem.html',
    styleUrls: ['./purchaselist-item.less']
})

export class TpPurchaseitemComponent extends TiBaseComponent {
    /**
     * 商品信息是否带有region
     */
    @Input() hasArea: boolean = false;
    /**
     * 单个产品右侧叉号点击事件
     */
    @Output() readonly close: EventEmitter<any> = new EventEmitter();
    /**
     * @ignore
     */
    public closeFn(): void {
        this.close.emit();
    }
}
