import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {
  TiAlertModule,
  TiButtonModule,
  TiCheckboxModule,
  TiIconModule,
  TiLocaleModule,
  TiOutlineModule,
  TiTextareaModule,
  TiTextModule,
  TiValidationModule
} from '@cloud/tiny3';

import { TpHalfmodalModule } from '../halfmodal/TpHalfmodalModule';
import { TpScoreModule } from '../score/TpScoreModule';
import { TpIconModule } from '../icon/TpIconModule';

import { TpFeedbackComponent } from './TpFeedbackComponent';
import { TpFeedbackContentComponent } from './TpFeedbackContentComponent';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    TiLocaleModule,
    TiAlertModule,
    TiButtonModule,
    TiCheckboxModule,
    TiIconModule,
    TiTextareaModule,
    TiTextModule,
    TiValidationModule,
    TiOutlineModule,
    TpHalfmodalModule,
    TpScoreModule,
    TpIconModule
  ],
  exports: [TpFeedbackComponent, TpFeedbackContentComponent],
  declarations: [TpFeedbackComponent, TpFeedbackContentComponent ],
})

export class TpFeedbackModule { }
export { TpFeedbackComponent } from './TpFeedbackComponent';
export { TpFeedbackContentComponent } from './TpFeedbackContentComponent';
export { TpFeedbackOptions, TpQuestionOption, TpAllquestionOption } from './TpFeedbackInterface';
