import { Component, ViewEncapsulation } from '@angular/core';
import { TiModalBodyComponent } from '@cloud/tiny3'

/**
 *
 *  业务典型场景用到的弹窗组件
 *
 *  10.1.0新增
 *
 *  提供了UI规范推荐的四种尺寸的弹窗样式，还有带alert提示的弹窗和典型confirm弹窗的样式
 *
 *  10.0.4及之前的版本是以模板的形式提供(相关的样式是全局的）；10.1.0版本为了实现样式按需加载，去掉了之前的模板，将其封装在一个组件模块，所以该组件实际只提供了样式。
 *
 *  <example-url>../tinyplus3demo/#/modalbody/modalbody-all</example-url>
 *
 */
@Component({
    selector: 'tp-modal-body', // 兼容延用之前的样式选择器
    template: '<ng-content></ng-content>',
    styleUrls: ['./modalbody.less'],
    encapsulation: ViewEncapsulation.None,
    host: {
        '[class.ti3-modal-body]': 'true' // 1. tiny3中的 .ti3-modal-body 有些样式；2. TiModalComponent 中有通过该样式类获取dom的逻辑
    }
})
export class TpModalBodyComponent extends TiModalBodyComponent { }
