import { NgModule } from '@angular/core';
import { TpSmartalertComponent } from './TpSmartalertComponent';
import { CommonModule } from '@angular/common';
import { TiIconModule } from '@cloud/tiny3';
/**
 * @ignore
 * 规范提出，云业务通用场景不会使用该组件，10.1.3起，在官网隐藏该组件。
 */
@NgModule({
  imports: [
    CommonModule,
    TiIconModule
  ],
  exports: [TpSmartalertComponent],
  declarations: [TpSmartalertComponent]
})

export class TpSmartalertModule {}
export { TpSmartalertComponent } from './TpSmartalertComponent';
