import {
    Component, ElementRef, EventEmitter, Input, Output, Renderer2, TemplateRef, ViewChild
    // tslint:disable-next-line: no-implicit-dependencies
} from '@angular/core';
// tslint:disable-next-line: no-implicit-dependencies
import { TiBaseComponent, TiTipRef, TiTipService, TiTipShowInfo, Util } from '@cloud/tiny3';
// tslint:disable-next-line: no-implicit-dependencies
import { CfUtil } from '../../cfutils/CfUtil';

/**
 * @ignore
 * 每条受限信息
 */
export interface TpFrozenInfo {
    /**
     * 1 冻结，2 解冻结
     */
    status: number;
    /**
     * 1-表示公安冻结, 2表示未实名认证冻结,3-表示欠费冻结,4-合作伙伴冻结,5-账号销户前冻结,6-违规冻结
     */
    type: number;
    /**
     * json 字符串，可以解析为一个数组，包含两个冻结信息
     * 详情参考 云运营公共接口文档 4.10 查询账号冻结场景
     */
    statusMsg: string;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;

}

/**
 * @ignore
 * 消息类型，'POP' 为提示块中的消息，'TIP' 为tip 提示消息
 */
type TpFrozenMessageType = 'POP' | 'TIP';

/**
 * @ignore
 *
 */
export interface TpFrozenStatusMsg {
    /**
     * 消息类型，'POP' 为提示块中的消息，'TIP' 为tip 提示消息
     */
    msgType: TpFrozenMessageType;
    /**
     * 消息字符串
     */
    msgContent: string;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;

}
/**
 * @ignore
 * 4.10	查询账号冻结场景 http 返回数据类型
 */
export interface TpFrozenResponse {
    /**
     * 接口返回值设置
     */
    frozenInfos: Array<TpFrozenInfo>;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;
}

/**
 * 账号冻结组件，组件查询CBC查询账号冻结场景接口，获取冻结信息，展示块提示，给目标元素添加tip提示。
 *
 * 9.0.3版本新增
 *
 * <example-url>../tinyplus3demo/#/frozen/frozen-all</example-url>
 */
@Component({
    selector: 'tp-frozen',
    templateUrl: './frozen.html',
    styleUrls: ['./frozen.less']
})
export class TpFrozenComponent extends TiBaseComponent {
    /**
     * 添加tip的按钮元素
     */
    @Input() buttonElem: HTMLElement;
    /**
     * 获取数据成功事件
     */
    @Output() readonly response: EventEmitter<TpFrozenResponse | Error> = new EventEmitter<TpFrozenResponse | Error>();
    /**
     * @ignore
     * tip 内容template
     */
    @ViewChild('tipContentTemplate', { static: false }) tipContentTemplate: TemplateRef<any>;
    /**
     * @ignore
     * 内部alert 打开关闭
     */
    public open: boolean = false;
    /**
     * @ignore
     * alert 类型
     */
    public alertType: string = 'error';
    /**
     * @ignore
     * 内部alert类型图标
     */
    public typeIcon: boolean = true;
    /**
     * @ignore
     * 内部alert关闭图标
     */
    public closeIcon: boolean = false;
    /**
     * @ignore
     * alert 内容
     */
    public label: string = '';
    /**
     * @ignore
     * tip 提示内容
     */
    public tipContent: string;
    /**
     * @ignore
     * tip实例，销毁tip作用
     */
    private tipInstance: TiTipRef;
    /**
     * @ignore
     * 从http返回数据中获取内容字符串
     */
    public static getMsgContent(data: TpFrozenResponse, msgType: TpFrozenMessageType): string {
        if (data && Array.isArray(data.frozenInfos) && data.frozenInfos.length > 0) {
            // 返回数据中 frozenInfos按优先级排序，所以取第一个
            const frozenInfo: TpFrozenInfo = data.frozenInfos[0] as TpFrozenInfo;
            // status 1 冻结 2 解冻
            if (frozenInfo.status !== 1 || !Util.isString(frozenInfo.statusMsg)) {
                return;
            }
            try {
                frozenInfo.statusMsgs = JSON.parse(frozenInfo.statusMsg) as Array<TpFrozenStatusMsg>;
            } catch (error) {
                console.warn(error);

                return;
            }
            const msg: TpFrozenStatusMsg = frozenInfo.statusMsgs.find((item: TpFrozenStatusMsg) => {
                return item.msgType === msgType;
            });
            if (msg && Util.isString(msg.msgContent)) {
                return msg.msgContent;
            }
        }
    }

    private static getUserInfo(config?: any): Promise<string> {
        // config 本地测试使用，实际使用时请忽略此参数
        if (config) {
            return new Promise((resolve, reject) => {
                resolve(config.domainId);
            });
        } else {
            return CfUtil.getUser().then((user: any) => user.domainId);
        }
    }

    /**
     * @ignore
     * 请求账号冻结场景接口
     * config 本地测试使用，实际使用时请忽略此参数
     */
    public static queryFrozenInfo(config?: any): Promise<TpFrozenResponse> {
        const queryFrozenInfoPromise: Promise<TpFrozenResponse> = TpFrozenComponent.getUserInfo(config)
            .then((domainId: string) => {
                const camel: any = config ? config.camel : CfUtil.getHttpService();
                const customerId: string = domainId;
                if (customerId && camel) {
                    let url: string = '/rest/cbc/cbccustmgrservice/v1/customer/frozen';
                    // AppWebPath 为console.home框架的设置,例如'/console'路径
                    const appWebPath: string = CfUtil.getAppWebPath();
                    if (Util.isString(appWebPath)) {
                        url = appWebPath + url;
                    }

                    return camel.get({
                        url: {
                            s: url
                        },
                        params: { customerId }
                    });
                } else {
                    return Promise.reject(new Error('frozen 依赖camel服务，使用前请先注入'));
                }
            });

        return queryFrozenInfoPromise;
    }

    constructor(
        protected hostRef: ElementRef,
        protected renderer2: Renderer2,
        private tipService: TiTipService
    ) {
        super(hostRef, renderer2);
    }

    /**
     * 查询冻结
     * config 为方便测试设置，实际使用时请忽略
     */
    public queryFrozen(config?: any): void {
        TpFrozenComponent.queryFrozenInfo(config)
            .then((response: any) => {
                this.label = TpFrozenComponent.getMsgContent(response, 'POP');
                // 是字符串显示
                this.open = Util.isString(this.label);

                // 给按钮添加tip
                this.tipContent = TpFrozenComponent.getMsgContent(response, 'TIP');
                if (Util.isString(this.tipContent) && this.buttonElem) {
                    this.addTip(this.buttonElem);
                }

                if (!this.label && !this.tipContent) {
                    this.unfrozen();
                }
                // 获取数据成功，触发response事件，将后台原始响应数据传递出去，方便使用者自行处理相关逻辑
                this.response.emit(response);
            }, (error: Error) => {
                this.response.emit(error);
                this.open = false;
            });
    }

    /**
     * 隐藏提示块,销毁tip
     */
    private unfrozen(): void {
        this.open = false;
        // @ts-ignore:忽略destroy检查
        if (this.tipInstance && Util.isFunction(this.tipInstance.destroy)) {
            // @ts-ignore:外层有判断
            this.tipInstance.destroy();
            this.tipInstance = undefined;
        }
    }
    /**
     *  给目标元素添加tip
     *  v9.0.4 版本添加
     */
    public addTip(...targets: Array<HTMLElement>): void {
        if (this.tipContentTemplate && this.tipContent) {
            targets.forEach((element: HTMLElement) => {
                this.tipInstance = this.tipService.create(element, {
                    trigger: 'mouse',
                    showFn: (): TiTipShowInfo => {
                        return {
                            content: this.tipContentTemplate
                        };
                    }
                });
            });
        }
    }
}
