import { Component, ElementRef, Input, OnInit, Renderer2 } from '@angular/core';
import { TiBaseComponent, TiLocale, Util } from '@cloud/tiny3';

import { CfUtil } from '../../cfutils/CfUtil';

import { TpUtil } from './../../utils/TpUtil';

export interface TpQuestionInfo {
    /**
     * 问题Id
     *
     */
    questionId: string;
    /**
     * 问题的类型，有radio和essay、两种类型
     *
     */
    type: 'radio' | 'essay';
    /**
     * 问题是否呈现
     */
    show: boolean;
    /**
     * 问题是否是必选
     *
     */
    require: boolean;
    /**
     * 问题的名称
     *
     */
    title: string;
}

export interface TpSurveyOptions {
    /**
     * 服务Id，各个服务需要唯一
     *
     */
    serviceId: string;
    /**
     * 触点Id，一个服务内的触点Id需要保证唯一
     *
     */
    contactId: string;
    /**
     * 触点名称，用于拼接问题
     *
     */
    contactName: string;
}
/**
 * 满意度调查组件
 *
 * 10.0.3版本新增
 *
 * <example-url>../tinyplus3demo/#/contactsurvey/contactsurvey-all</example-url>
 */
@Component({
    selector: 'tp-contactsurvey',
    templateUrl: './contactsurvey.html',
    styleUrls: ['./contactsurvey.less'],
    host: {
        class: 'tp-contactsurvey-container' // 给最外层元素加样式类
    }
})

export class TpContactsurveyComponent extends TiBaseComponent implements OnInit {
    /**
     * 常量配置(90天对应的毫秒数)
     */
    private static readonly MILLISECONDS_PER_MONTH: number =  90 * 24 * 60 * 60 * 1000;
    /**
     * 组件传入数据
     *
     */
    @Input() options: TpSurveyOptions;
    /**
     * @ignore
     * 是否显示满意度调查组件，默认不显示。符合某些条件才会展示出来。
     */
    public showContactsurvey: boolean = false;
    /**
     * @ignore
     * 控制提交按钮是否禁用，默认为禁用
     */
    public submitDisabled: boolean = true;
    /**
     * @ignore
     * 国际化词条对象
     */
    public tpContactsurvey: any = TiLocale.getLocaleWords().tpContactsurvey;
    /**
     * @ignore
     * 评分值
     */
    public scoreValue: number;
    /**
     * @ignore
     * 文本框组件输入值
     */
    public textareaValue: string;
    /**
     * @ignore
     * 模板中展示的题目
     */
    public displayData: Array<TpQuestionInfo> = [];
    /**
     * @ignore
     * 用户反馈的答案
     */
    public submitAnswer: Array<any> = [];
    /**
     * 类生产环境的请求数据
     */
    private devConfig: any = {
        backend: 'https://console.ulanqab.huawei.com/nps-api',
        surveyId: 'hwcloudbusurvey_key_fbd25bdbdb89'
    };
    /**
     * 现网环境的请求数据
     */
    private prodConfig: any = {
        backend: 'https://voc.huaweicloud.com/survey-api',
        surveyId: 'hwcloudbusurvey_key_fbd25bdbdb89'
    };
    /**
     * 用户ID，作为用户的唯一标识
     */
    private userId: string = '';
    /**
     * 默认为类生产环境配置
     */
    public config: any = this.devConfig;
    constructor(private elementRef: ElementRef, private renderer2: Renderer2) {
        super(elementRef, renderer2);
        if (location.hostname.indexOf('huaweicloud.com') > -1) {
            this.config = this.prodConfig;
        }
    }

    ngOnInit(): void {
        super.ngOnInit();
        CfUtil.getUser()
            .then((user: any): void => {
                const root: any = user;
                const camel: any = CfUtil.getHttpService();
                const msgService: any = CfUtil.getMessage();
                if (!camel || !msgService || !root || !Util.isFunction(camel.get) || !Util.isFunction(msgService.showSuccess)) {
                    return;
                }
                const userId: string = root.domainId;
                this.userId = userId;
                const url: string = `${this.config.backend}/api/get/commit/date?surveyId={survey_id}&userId={user_id}&serviceId={service_id}&contactId={contact_id}`;
                if (userId) {
                    camel.get({
                        url: {
                            s: url,
                            o: {
                                survey_id: this.config.surveyId,
                                user_id: userId,
                                service_id: this.options.serviceId,
                                contact_id: this.options.contactId
                            }
                        }
                    })
                    .then((response: any): void => {
                        // 后台返回的日期格式yyyy-MM-dd，在IE下Date.parse为NaN，导致组件提交成功后还会出现。需要转成yyyy/mm/dd，目前这种格式在3个浏览器都OK
                        const submitDate: any = Date.parse(response.data.replace(/-/g, '/'));
                        // 如果是非法日期，或者没有提交为''，则重新赋值为0。否则会出现NaN，组件展示不出来。
                        const milliSeconds: number = isNaN(submitDate) ? 0 : submitDate;

                        // 查询上次提交的日期，如果小于90天则不显示。否则，看之前用户是否点击关闭按钮
                        if (new Date().getTime() - milliSeconds > TpContactsurveyComponent.MILLISECONDS_PER_MONTH) {
                            const tinyContactSurvey: string =
                            Util.isUndefined(TpUtil.getCookie('tinyContactSurvey')) ? '' : TpUtil.getCookie('tinyContactSurvey');

                            if (tinyContactSurvey.indexOf(`${userId}${this.options.serviceId}${this.options.contactId}`) === -1) {
                                this.showContactsurvey = true;
                            }
                        }
                    }, (): void => {
                        // 数据请求失败，不出现该组件。如果出现错误提示，会给用户带来困扰。
                    });
                }
            });
        // 在模板中用于展示的问题数据
        this.displayData = [{
            questionId: 'question1',
            type: 'radio',
            show: true, // 初始是否需要显示
            require: true, // 是够是必选
            title: Util.formatEntry(this.tpContactsurvey.contactSurvey_question1_title, [this.options.contactName])
        }, {
            questionId: 'question2',
            type: 'essay',
            show: false,
            require: false,
            title: this.tpContactsurvey.contactSurvey_question2_title
        }];
    }
    /**
     * @ignore
     *  叉号图标点击事件
     */
    public onClickClose(): void {
        const closedList: string = Util.isUndefined(TpUtil.getCookie('tinyContactSurvey')) ? '' : TpUtil.getCookie('tinyContactSurvey');
        TpUtil.setCookie('tinyContactSurvey', `${closedList}${this.userId}${this.options.serviceId}${this.options.contactId},`);
        this.showContactsurvey = false;
    }
    /**
     * @ignore
     * 调查结果提交事件
     */
    public onClickSubmit(): void {
        CfUtil.getUser().then((user: any): void => {
            const root: any = user;
            const camel: any = CfUtil.getHttpService();
            const msgService: any = CfUtil.getMessage();
            if (!camel || !msgService || !root || !Util.isFunction(camel.post) || !Util.isFunction(msgService.showSuccess)) {
                return;
            }
            const url: string = `${this.config.backend}/api/save`;
            // 发送请求，向后台发送问卷结果
            const sendData: any = {
                data: {
                    surveyId: this.config.surveyId,
                    serviceId: this.options.serviceId,
                    contactId: this.options.contactId,
                    w3account: root.domainId, // 用户ID
                    answers: this.submitAnswer
                }
            };

            camel.post({
                url: {
                    s: url
                },
                params: sendData
            })
            .then((): void => { }, (): void => { });

            this.showContactsurvey = false;
            msgService.showSuccess(this.tpContactsurvey.contactSurvey_submit_success);
        }, (): void => { });
    }
    /**
     * @ignore
     * 点击评分组件
     */
    public changeScore(score: number): void {
        if (!this.displayData[1].show) {
            this.displayData[1].show = true;
        }

        this.initAnswerArrray();

        // 对于单选来说，answer和subRemark的值一样
        this.submitAnswer[0].answer = score;
        this.submitAnswer[0].subRemark = score;

        this.submitDisabled = false;
    }
    /**
     * @ignore
     * 文本框失焦事件
     */
    public onBlur(value: string): void {
        this.submitAnswer[1].answer = value;
    }
    /**
     * @ignore
     * 初始化答案对象，用于给后台下发
     */
    public initAnswerArrray(): void {
        for (let i: number = 0; i < this.displayData.length; i++) {
            this.submitAnswer[i] = { };
            this.submitAnswer[i].questionId = this.displayData[i].questionId;
            this.submitAnswer[i].subQuestionId = null;
            this.submitAnswer[i].subName = this.displayData[i].title;
            this.submitAnswer[i].answer = '';
            this.submitAnswer[i].subRemark = '';
            if (this.displayData[i].type === 'essay') {
                this.submitAnswer[i].subRemark = null;
            }
            this.submitAnswer[i].reason = '0';
        }
    }
}
