import { Component, Input, ViewChild, ElementRef } from '@angular/core';
import { TiLocale, TiValidationConfig, TiBaseComponent } from '@cloud/tiny3';

/**
 * @ignore
 */
@Component({
    templateUrl: './TpRenameModalComponent.html',
    styleUrls: ['./renamemodal.less']
})

export class TpRenameModalComponent extends TiBaseComponent {
    @Input() value: string;
    @Input() modalHeader: string;

    @ViewChild('input', { static: true }) input: ElementRef;

    public okLabel: string = TiLocale.getLocaleWords().tpRename.okButton;
    public cancelLabel: string = TiLocale.getLocaleWords().tiCommon.cancelBtn;
    public model: string;

    public validation: TiValidationConfig = {
        errorMessage: {
            regExp: TiLocale.getLocaleWords().tpRename.inValidMessage
        }
    };
    // 此处的close并不是按钮真正的close()，参数form的类型本应是FormGroup，所以没有引入FormGroup，指定为any
    close(form: any): void {
    }
    dismiss(): void {
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.model = this.value;
    }
    ngAfterViewInit(): void {
        this.input.nativeElement.focus();
        this.input.nativeElement.setSelectionRange(0, 0, 'backward');
    }

}
