// tslint:disable-next-line:no-implicit-dependencies
import { Component, ContentChild, Input, SimpleChanges, TemplateRef } from '@angular/core';
import { TiBaseComponent, Util } from '@cloud/tiny3';
export interface TpTimelineOption {
    /**
     * 设置时间轴条目对应的轴心标识，不同状态对应的标识颜色不一样
     */
    type?: 'info' | 'success' | 'danger' | 'primary' | 'warning';
    /**
     * 设置时间轴条目的文本。
     */
    label: string;
    /**
     * 设置时间轴的时间。
     */
    time?: string;
    /**
     * 是否为时间轴标题
     * 10.1.4新增
     */
    isTitle?: boolean;
    /**
     * 执行失败错误信息
     * 10.1.4新增
     */
    errorMessage?: string;
    /**
     * 时间轴问号tip提示
     * 10.1.4新增
     */
    iconTip?:  string | TemplateRef<any> | any;
    /**
     * tip方向
     * 10.1.4新增
     */
    iconTipPosition?: string;
    /**
     * tip最大宽度
     * 10.1.4新增
     */
    iconTipMaxWidth?: string;

}
/**
 * 时间线timeline组件
 * <example-url>../tinyplus3demo/#/timeline/timeline-all</example-url>
 */
@Component({
    selector: 'tp-timeline',
    templateUrl: './timeline.html',
    styleUrls: ['./timeline.less']
})

export class TpTimelineComponent extends TiBaseComponent {
    /**
     * 时间轴配置项总接口
     */
    @Input() options: Array<TpTimelineOption>;
    /**
     * 当前激活项（正在进行时）
     * 10.1.4新增
     */
    @Input() activeIndex: number = -1;
    /**
     * @ignore
     */
    @ContentChild('item', { static: true }) itemTemplate: TemplateRef<any>;
    /**
     * @ignore
     * 时间轴一级信息集合
     */
    public titleOptions = [];

    ngOnInit(): void {
        super.ngOnInit();
        this.titleOptions = this.getTitleOptions();
        this.changeToSuccess();
    }
    ngOnChanges(changes: SimpleChanges): void {
        if (changes['activeIndex'] && !changes['activeIndex'].firstChange) {
            this.changeToSuccess();
        }
        if (changes['options'] && !changes['options'].firstChange) {
            this.titleOptions = this.getTitleOptions();
        }
    }
    /**
     * @ignore
     */
    public findItemIndex(item: TpTimelineOption) {
        return this.titleOptions.findIndex( (ele: TpTimelineOption) => ele === item);
    }
    /**
     * @ignore
     * 获取时间轴一级（标题）信息集合
     */
    public getTitleOptions() : Array<TpTimelineOption> {
        return this.options.filter( (option: TpTimelineOption) => option.isTitle);
    }
    /**
     * @ignore
     * activeIndex之前的项全部变更为success状态
     */
    public changeToSuccess(): void {
        if (this.activeIndex === -1 || Util.isUndefined(this.activeIndex) || !Util.isNumber(this.activeIndex)) {
            return;
        }
        this.options.forEach ( (option: TpTimelineOption, index: number) => {
            option.type = index < this.activeIndex ? 'success' : 'primary';
        })
    }
    /**
     * @ignore
     * 判断当前一级时间轴状态
     */
    public isSuccess(option: TpTimelineOption): boolean {
        if(!option.isTitle) {
            return;
        }
        let level2Options: Array<TpTimelineOption> = [];
        const index: number = this.options.findIndex((item: TpTimelineOption) => item === option);
        let isSucess: boolean = true;
        for (let i = index + 1; i <= this.activeIndex; i++) {
            if (this.options[i] && this.options[i].isTitle) {
                break;
            }
            level2Options.push(this.options[i]);
        }
        if (level2Options.length > 0) {
            level2Options.forEach((option: TpTimelineOption) => {
                if (option.type && option.type !== 'success') {
                    isSucess = false;
                }
            })
        }

        return isSucess;
    }
}
