import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'isMajorPrice' })
export class IsMajorPricePipe implements PipeTransform {
    transform(value: Array<any>): Array<any> {
        return value.filter((item: any) => item.majorPrice === true);
    }
}

@Pipe({ name: 'isMinorPrice' })
export class IsMinorPricePipe implements PipeTransform {
    transform(value: Array<any>): Array<any> {
        return value.filter((item: any) => item.majorPrice === false);
    }
}
