import { Component, ElementRef, Renderer2, TemplateRef, ViewChild } from '@angular/core';
// tslint:disable-next-line: no-implicit-dependencies
import { TiBaseComponent, TiLocale, TiModalService } from '@cloud/tiny3';

@Component({
    selector: 'tp-searchbox-help',
    templateUrl: './searchbox-help.html',
    styleUrls: ['./searchbox-help.less']
})
export class TpSearchboxHelpComponent extends TiBaseComponent {
    /**
     * @ignore
     * 默认字段
     */
    public defaultField: string = 'testName';
    /**
     * @ignore
     * 示例1选中项
     */
    public tags1: Array<any>;
    /**
     * @ignore
     * 示例二选中项
     */
    public tags2: Array<any>;
    /**
     * @ignore
     * 示例二选中项
     */
    public tags3: Array<any>;
    /**
     * @ignore
     * 输入项
     */
    public items: Array<any> = [];
    /**
     * @ignore
     * 弹窗模板
     */
    @ViewChild(TemplateRef, { static: false }) helpModalTemplateRef: TemplateRef<any>;

    constructor(hostRef: ElementRef, render: Renderer2, private tiModal: TiModalService) {
        super(hostRef, render);
    }

    // tslint:disable-next-line: use-life-cycle-interface
    ngOnInit(): void {
        super.ngOnInit();
        // tslint:disable-next-line: typedef
        const { name,
            status,
            running,
            creating,
            stopped,
            restarting,
            tag } = TiLocale.getLocaleWords().tpSearchbox;
        // 初始化数据
        this.items = [{
            label: name,
            field: 'testName',
            type: 'radio',
            options: [{
                label: 'ecs-1'
            }, {
                label: 'obs-2'
            }, {
                label: 'vpc-1'
            }]
        },
        {
            label: status,
            field: 'testStatus',
            type: 'checkbox',
            options: [{
                label: running
            }, {
                label: creating
            }, {
                label: stopped
            }, {
                label: restarting
            }]
        },
        {
            id: '3',
            label: tag,
            field: 'testTag',
            type: 'map',
            options: [
                {
                    label: 'aaa',
                    options: [
                        {
                            label: 'ddd'
                        },
                        {
                            label: 'eee'
                        },
                        {
                            label: 'fff'
                        }
                    ]
                },
                {
                    label: 'bbb',
                    options: [
                        {
                            label: 'ddd'
                        },
                        {
                            label: 'eee'
                        },
                        {
                            label: 'fff'
                        }
                    ]
                },
                {
                    label: 'ccc',
                    options: [
                        {
                            label: 'ddd'
                        },
                        {
                            label: 'eee'
                        },
                        {
                            label: 'fff'
                        }
                    ]
                }
            ]
        }];
        this.tags1 = [
            {
                field: 'testName',
                label: name,
                value: 'ecs-1',
                type: 'radio'
            }
        ];
        this.tags2 = [
            {
                field: 'testName',
                label: name,
                value: 'ecs-1',
                type: 'radio'
            },
            {
                field: 'testStatus',
                label: status,
                value: `${running} | ${creating} | ${stopped}`,
                type: 'checkbox'
            }
        ];
        this.tags3 = [
            {
                field: 'testName',
                label: name,
                value: 'ecs-1',
                type: 'radio'
            },
            {
                field: 'testStatus',
                label: status,
                value: running,
                type: 'checkbox'
            },
            {
                field: 'testTag',
                label: tag,
                value: 'aaa=',
                type: 'map'
            }
        ];
    }
    /**
     * 展示弹窗
     */
    public show(): void {
        this.tiModal.open(this.helpModalTemplateRef, {
            id: 'tp-searchbox-help-modal',
            modalClass: 'tp-searchbox-help-modal'
        });
    }
}
