import { ChangeDetectorRef, Component, ElementRef, Renderer2, ViewChild } from '@angular/core';
import { TiBaseComponent, TiLocale, Util } from '@cloud/tiny3';
import { AccountService } from '@cloud/cfdata';
import { Subscription } from 'rxjs';

/**
 * 区域服务维护页面组件
 *
 * 该组件与console框架相关，框架处理数据，组件获取数据显示；
 * 使用时只需引入该组件，无需配置属性接口。
 *
 * <b>注</b>：支持列表中的region区域是服务已配置的可支持的区域。
 *
 * <example-url>../tinyplus3demo/#/beingmaintained/beingmaintained-all</example-url>
 */
@Component({
    selector: 'tp-beingmaintained',
    templateUrl: 'beingmaintained.html',
    styleUrls: ['beingmaintained.less'],
})

export class TpBeingmaintainedComponent extends TiBaseComponent {
    /**
     * @ignore
     */
    @ViewChild('container') containerEle: ElementRef;
    /**
     * @ignore
     */
    maintainRegions: Array<any>;
    /**
     * @ignore
     */
    label: string;
    /**
     * @ignore
     */
    maintainTime: string;
    /**
     * @ignore
     */
    describe: string;
    private regionsDataSub: Subscription;
    private tpBeingmaintained: any = TiLocale.getLocaleWords().tpBeingmaintained;

    constructor(
        private elementRef: ElementRef,
        private renderer2: Renderer2,
        private changeDetector: ChangeDetectorRef) {
        super(elementRef, renderer2);
    }
    ngOnInit(): void {
        super.ngOnInit();
        this.setRegionsData();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        if (this.regionsDataSub) {
            this.regionsDataSub.unsubscribe();
        }
    }

    private setRegionsData(): void {
        if (!(window as any).cfRegionSubject) {
            return;
        }

        this.regionsDataSub = (window as any).cfRegionSubject.subscribe({
            next: (items: { region: any, maintainRegions: Array<any> }): void => {
                if (items.region && items.region.displayName) {
                    const displayName: any = `<span>${items.region.displayName}</span>`;
                    this.label = Util.formatEntry(this.tpBeingmaintained.label, [displayName]);
                    this.getMaintainData().then((maintainData: { time: string, describe: string }) => {
                        if (maintainData) {
                            if (!Util.isEmptyString(maintainData.time)) {
                                this.maintainTime = `${this.tpBeingmaintained.maintainTime}${maintainData.time}`;
                            }
                            this.describe = maintainData.describe;
                        }
                    });
                }

                this.maintainRegions = items.maintainRegions;
                this.changeDetector.detectChanges();
            }
        });
    }

    private getMaintainData(): Promise<any> {
        return AccountService.queryCustomMaintenances().then((data: any) => {
            let time: string = '';
            let describe: string = '';
            if (data) {
                time = data.startTime + ' - ' + data.endTime;
                describe = data.describe;
            }
            return { time, describe };
        });
    }
}
