import { Component, Input, TemplateRef } from '@angular/core';
import { TiBaseComponent, TiPositionType } from '@cloud/tiny3';

/**
 * 描述及购买组件，用于解释说明服务，并提供了购买等操作按钮
 *
 * <example-url>../tinyplus3demo/#/descandbuy/descandbuy-all</example-url>
 */

export interface TpDescandbuyConfig {
    /**
     * helptip的配置对象
     *
     * 包含label、iconTip、iconTipPosition、iconTipMaxWidth（9.0.4 版本新增）属性，参考[TpHelptip组件]{@link ../components/TpHelptipComponent.html}
     */
    helptip: {
        label?: string;
        iconTip?: string | TemplateRef<any> | any;
        iconTipPosition?: TiPositionType;
        iconTipMaxWidth?: string;
    };
    buttons: Array<Button>;
    /**
     * 价格计算器的配置对象
     *
     * 包含values对象数组、url、detailHref三个属性换和beforeClick()方法，
     * 参考[TpPricecalc组件]{@link ../components/TpPricecalcComponent.html}
     */
    pricecalc?: {
        values?: Array<{
            key: string;
            value: string;
        }>;
        url?: string;
        detailHref?: string;
        beforeClick?(): void;
    };
}

export interface Button {
    /**
     * 按钮ID
     */
    id?: string;
    /**
     * 按钮文本
     */
    label: string;
    /**
     * 按钮颜色：默认按钮/强调色按钮/主题色按钮，参考[tiny3Button组件]{@link ../../tiny3doc/components/TiButtonComponent.html}
     */
    color?: string;
    /**
     * tinyPlus图标名称
     */
    iconName?: string;
    /**
     * 按钮禁用 9.0.4版本新增
     */
    disabled?: boolean;
    /**
     * tip内容 9.0.4版本新增
     */
    tip?: string | TemplateRef<any>;
    /**
     * tip方位 9.0.4版本新增
     */
    tipPosition?: TiPositionType;
    /**
     * 按钮是否显示 10.1.3版本新增
     */
    show?: boolean;
    /**
     * 点击事件
     */
    click?(): void;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;
}

@Component({
    selector: 'tp-descandbuy',
    templateUrl: './descandbuy.html',
    styleUrls: ['./descandbuy.less']
})

export class TpDescandbuyComponent extends TiBaseComponent {
    /**
     * 组件的配置对象，详情可以参考TpDescandbuyConfig接口
     */
    @Input() config: TpDescandbuyConfig;

    /**
     * @ignore
     */
    public onButtonClick(button: Button): void {
        if (typeof button.click === 'function') {
            button.click();
        }
    }
    /**
     * @ignore
     */
    public onPricecalcClick(): void {
        if (typeof this.config.pricecalc.beforeClick === 'function') {
            this.config.pricecalc.beforeClick();
        }
    }
}
