import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
/**
 *  特殊卡片组件，添加卡片组件
 *
 * <example-url>../tinyplus3demo/#/card/card-all</example-url>
 */
@Component({
    selector: 'tp-card-add',
    templateUrl: './card-add.html',
    styleUrls: ['./card-add.less'],
    host: {
        '[class.tp-card-add-disabled]': 'disabled',
        '[attr.tabindex]': 'disabled? "-1":"0"'
    }
})

export class TpCardAddComponent extends TiBaseComponent {
    /**
     * 禁用状态
     */
    @Input() disabled: boolean = false;
    /**
     * 点击事件
     */
    @Output() readonly add: EventEmitter<any> = new EventEmitter<any>();

    /**
     * @ignore
     * 加号点击回调
     */
    public onClick($event: Event): void {
        if (this.disabled) {
            return;
        }
        this.add.emit($event);
    }
}
