import { Component, ViewEncapsulation } from '@angular/core';

/**
 * Selectsearchbox组件由下拉选择组件和搜索框组件构成。
 *
 * Selectsearchbox组件使用的时候需要和下拉框（TiSelect）及搜索框（TiSearchBox）一起使用。
 *
 * API详情请参考下拉框（TiSelect）及搜索框（TiSearchBox）。
 *
 * <example-url>../tinyplus3demo/#/selectsearchbox/selectsearchbox-all</example-url>
 */
@Component({
    selector: 'tp-selectsearchbox',
    template: '<ng-content></ng-content>',
    styleUrls: ['./selectsearchbox.less'],
    encapsulation: ViewEncapsulation.None,
    host: {
        '[class.tp-selectsearchbox-container]': 'true'
    }
})

export class TpSelectsearchboxComponent {
}
