import { Component, EventEmitter, Input, Output } from '@angular/core';
import { TpResourceItem } from './TpResourceItemComponent';
import { TiBaseComponent } from '@cloud/tiny3';

/**
 * 资源列表 resources
 *
 * <example-url>../tinyplus3demo/#/resources/resources-all</example-url>
 */
@Component({
    selector: 'tp-resources',
    templateUrl: './resources.html',
    styleUrls: ['./resources.less']
})

export class TpResourcesComponent extends TiBaseComponent {
    /**
     * 资源列表标题
     */
    @Input() title: string;
    /**
     * 列表对象主接口；用于列表中每个内容中参数的传递。
     */
    @Input() items: Array<TpResourceItem>;
    /**
     * 选中某项资源时，触发事件
     */
    @Output() readonly select: EventEmitter<TpResourceItem> = new EventEmitter<TpResourceItem>();
    /**
     * 选中某项资源异常数时，触发事件
     */
    @Output() readonly errorSelect: EventEmitter<TpResourceItem> = new EventEmitter<TpResourceItem>();

    /**
     * @ignore
     */
    public onClick(item: TpResourceItem): void {
        this.select.emit(item);
    }
    /**
     * @ignore
     */
    public onErrorSelect(item: TpResourceItem): void {
        this.errorSelect.emit(item);
    }
}
