import { Injectable } from '@angular/core';
import { TpIdentificationModule } from './TpIdentificationModule';
import { TiLocale, TiMessageType } from '@cloud/tiny3';
import { TpGoandbackService } from '../goandback/TpGoandbackService';

/**
 * 认证组件  identification，使用该服务时需要引入模块TpIdentificationModule，导入服务TpIdentificationSerive,通过调用TpIdentificationService.open(config)方法生成认证内容
 *
 * <example-url>../tinyplus3demo/#/identification/identification-all</example-url>
 */
@Injectable({
    providedIn: TpIdentificationModule
})
export class TpIdentificationService extends TpGoandbackService {
    protected words: any = TiLocale.getLocaleWords().tpIdentification;
    protected firstMessageType: TiMessageType = 'prompt';
}
