import {
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Input,
  NgZone,
  Output,
  QueryList,
  Renderer2,
  SimpleChange,
  SimpleChanges,
  ViewChild
  // tslint:disable-next-line: no-implicit-dependencies
} from '@angular/core';

// tslint:disable-next-line: no-implicit-dependencies
import { TiBaseComponent } from '@cloud/tiny3';
// tslint:disable-next-line: no-implicit-dependencies
import { TpCardComponent } from '../card/TpCardModule';
/**
 * @ignore
 * 规范提出，云业务通用场景不会使用该组件，10.1.3起，在官网隐藏该组件。
 */
export interface TpSlidecardsPaginationEvent {
  currentPage: number;
  pageSize: number;
  totalPage: number;
  /**
   * 允许有多余的属性字段
   */
  [propName: string]: any;
}

/**
 * @ignore
 * 规范提出，云业务通用场景不会使用该组件，10.1.3起，在官网隐藏该组件。
 * 滚动卡片列表，功能与轮播图相似，可以显示多屏展示卡片的效果，并且还提供了增加、删除卡片功能。详情参考测试用例。
 *
 * <example-url>../tinyplus3demo/#/slidecards/slidecards-all</example-url>
 */
@Component({
  selector: 'tp-slidecards',
  templateUrl: './slidecards.html',
  styleUrls: ['./slidecards.less']
})
export class TpSlidecardsComponent extends TiBaseComponent {
  /**
   * 当前选中卡片在数组中的下标
   */
  @Input() currentIndex: number = 0;
  /**
   * 当前页改变事件
   */
  @Output() readonly pageChange: EventEmitter<TpSlidecardsPaginationEvent> = new EventEmitter<TpSlidecardsPaginationEvent>();
  /**
   * @ignore
   * 卡片的可视区
   */
  @ViewChild('listWrapper') listWrapperEleRef: ElementRef;
  /**
   * @ignore
   * 卡片父元素，用于监听内容变化
   */
  @ViewChild('list') listEleRef: ElementRef;
  /**
   * @ignore
   * 所有的卡片集合
   */
  @ContentChildren(TpCardComponent, { descendants: false }) cardComRefs!: QueryList<TpCardComponent>;
  /**
   * @ignore
   * 当前页
   */
  public currentPage: number = 1;
  /**
   * 每个卡片的宽度，用于计算分页
   */
  private cardWidth: number;
  /**
   * 卡片之间的距离，用于计算分页
   */
  private cardSpan: number = 0;
  /**
   * @ignore
   * 卡片父元素的偏移距离，用于控制卡片左右滑动
   */
  public left: string = '0px';
  /**
   * @ignore
   * 分页页码显示数据
   */
  public pageList: Array<string | number> = [];
  /**
   * 每页卡片数
   */
  private pageSize: number = 1;
  /**
   * @ignore
   * 分页总页数
   */
  public totalPage: number = 1;
  /**
   * 总的卡片数量
   */
  private totalCard: number = 0;

  private unlistenResize: () => void;
  // 卡片父元素 MutationObserver 对象
  private listObserver: MutationObserver;

  constructor(
    protected hostRef: ElementRef,
    private renderer2: Renderer2,
    private zone: NgZone
  ) {
    super(hostRef, renderer2);
  }

  // tslint:disable-next-line: use-life-cycle-interface
  ngOnInit(): void {
    super.ngOnInit();
    // 一屏显示卡片的数目会随窗口大小而变化，所以监听窗口大小变化重新计算分页，
    this.zone.runOutsideAngular(() => {
      this.unlistenResize = this.renderer2.listen(window, 'resize', () => {
        if (this.canCalculatePaging()) {
          this.calculatePaging();
        }
      });
    });
  }

  /**
   * 因为初始化要展示当前索引卡片，所以在初始化后先计算一次分页，然后显示currentIndex卡片
   * 分页的计算依赖视图可视区的宽度，视图加载完成后才能计算，
   * 分页计算后，会改变数据，所以用setTimeout。
   *  总卡片数及cardsComRef 长度， pageSize = 容器宽度/（卡片宽度 + 卡片间的距离）
   */
  ngAfterViewInit(): void {
    super.ngAfterViewInit();
    // 初始化时计算一次分页，然后把当前索引的卡片显示出来
    if (this.canCalculatePaging()) {
        setTimeout(() => {
            this.calculatePaging();
            this.showCurrentCard(this.currentIndex);
        }, 0);
    }

    this.listObserver = new MutationObserver((mutationRecords) => {
        this.calculatePaging();
    });
    // MutationObserver 对象的回调方法开始接收与给定选项匹配的DOM变化的通知
    // 只需关注卡片增删和lef偏移值，所以只设置childList、attributes
    this.listObserver.observe(this.listEleRef.nativeElement, {
        childList: true, // 监视目标节点添加或删除新的子节点
        attributes: true, // 观察受监视元素的属性值变更,主要是为了监听 style属性
        attributeFilter: ['style'] // 要监视的特定属性名称的数组
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    super.ngOnChanges(changes);
    // currentIndex 改变跳转到对应页显示当前索引卡片
    const currentIndexChange: SimpleChange = changes['currentIndex'];
    if (!currentIndexChange.firstChange) {
      this.showCurrentCard(currentIndexChange.currentValue);
    }
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();
    // 组件销毁时，注销窗口resize事件监听
    if (this.unlistenResize) {
      this.unlistenResize();
    }
    if (this.listObserver) {
        this.listObserver.disconnect();
    }
  }
  /**
   * 是否能进行分页计算
   * @returns boolean
   */
  private canCalculatePaging(): boolean {
    return this.listWrapperEleRef && this.cardComRefs && this.cardComRefs.length > 0;
  }
  /**
   * 先计算分页每页的卡片数pageSize，再计算总页数totalPage
   */
  private calculatePaging(): void {
    this.calculatePageSize();
    this.calculateTotalPage();
  }
  /**
   * 计算分页总页数
   */
  private calculateTotalPage(): void {
    this.totalCard = this.cardComRefs.length;
    // 计算共有多少页
    this.totalPage = Math.ceil(this.totalCard / this.pageSize);
    // 生成分页器显示数组
    this.generatePagesArray();
    // 重新计算总页面后，需要处理当前页大于总页数的场景
    if (this.currentPage > this.totalPage) {
      this.goPage(this.totalPage);
    }
  }

  /**
   * 计算一次显示的卡片数
   */
  private calculatePageSize(): void {
    // getComputedStyle 获取的宽度，在ie11下不包含padding和border,所以使用getBoundingClientRect（）
    // 在初始化时，totalPage还没有计算出来，左右翻页按钮没有显示，但容器元素的可用宽度应该减去左右按钮的宽度之和30+30=60
    const containerWidth: number = (this.listWrapperEleRef.nativeElement as HTMLElement).getBoundingClientRect().width - 60;
    this.cardWidth = (this.cardComRefs.first.nativeElement as HTMLElement).getBoundingClientRect().width;
    const cardStyle: CSSStyleDeclaration = getComputedStyle(this.cardComRefs.first.nativeElement);
    this.cardSpan = parseFloat(cardStyle.marginRight);
    // 当容器宽度小于card宽度时,需处理，避免分页不能正常生成
    this.pageSize = containerWidth <= (this.cardWidth + this.cardSpan) ? 1 : Math.floor(containerWidth / (this.cardWidth + this.cardSpan));
  }

  /**
   * 生成分页器显示的数组
   */
  private generatePagesArray(): void {
    this.pageList = [];
    // 判断总页数如果小于等于分页的长度，若小于则直接显示
    if (this.totalPage <= 8) {
      for (let i: number = 1; i <= this.totalPage; i++) {
        this.pageList.push(i);
      }
    } else if (this.currentPage <= 3) {
      // 右边...
      for (let i: number = 1; i <= 5; i++) {
        this.pageList.push(i);
      }
      this.pageList.push('...');
      this.pageList.push(this.totalPage);
    } else if (this.currentPage >= this.totalPage - 3) {
      // 左边 ...
      this.pageList.push(1);
      this.pageList.push('...');
      for (let i: number = 4; i >= 1; i--) {
        this.pageList.push(this.totalPage - i);
      }
      this.pageList.push(this.totalPage);
    } else {
      // 最后一种情况，两边都有...
      this.pageList.push(1);
      this.pageList.push('...');
      for (let i: number = 2; i >= 1; i--) {
        this.pageList.push(this.currentPage - i);
      }
      this.pageList.push(this.currentPage);
      for (let i: number = 1; i < 2; i++) {
        this.pageList.push(this.currentPage + i);
      }
      this.pageList.push('...');
      this.pageList.push(this.totalPage);
    }
  }

  /**
   * 指定页码翻页
   * @param pageNum 跳转到的页码
   * @ignore
   */
  public goPage(pageNum: number | string): void {
    // ... 时，不做处理
    if (typeof pageNum === 'string') {
      return;
    }

    this.currentPage = pageNum;
    // 偏移量=(当前页-1)*每页的卡片数量*（卡片宽度 + 卡片的Margin)
    this.left = -(pageNum - 1) * this.pageSize * (this.cardWidth + this.cardSpan) + 'px';
    // 触发pageChange事件
    const data: TpSlidecardsPaginationEvent = {
      currentPage: pageNum,
      pageSize: this.pageSize,
      totalPage: this.totalPage
    };
    this.pageChange.emit(data);

  }

  /**
   * 向前跳转一页
   * @ignore
   */
  public goPrev(): void {
    if (this.currentPage > 1) {
      this.goPage(this.currentPage - 1);
    }
  }

  /**
   * 向后跳转一页
   * @ignore
   */
  public goNext(): void {
    if (this.currentPage < this.totalPage) {
      this.goPage(this.currentPage + 1);
    }
  }

  /**
   * 展示指定索引的卡片，在初始化及currentIndex 改变时，如果指定索引不在当前页，跳转到能显示c索引对应卡片的页码
   */
  private showCurrentCard(index: number): void {
    // 如果索引和卡片的数目不匹配，不做处理。
    if (index < 0 && index > (this.totalCard - 1)) {
      return;
    }
    // 当前一屏卡片的最大最小索引
    const maxIndex: number = this.currentPage * this.pageSize - 1;
    const minIndex: number = this.currentPage > 0 ? ((this.currentPage - 1) * this.pageSize - 1) : 0;
    // 当前索卡片是否在当前页面
    if (index < minIndex || index > maxIndex) {
      const targetPage: number = Math.ceil((index + 1) / this.pageSize);
      this.goPage(targetPage);
    }
  }

}
