import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { TpIconModule } from '../icon/TpIconModule';

import { TiOverflowModule } from '@cloud/tiny3';
import { TpSelectgroupComponent } from './TpSelectgroupComponent';
import { TpSelectitemComponent } from './TpSelectitemComponent';

@NgModule({
  imports: [
    CommonModule,
    TiOverflowModule,
    TpIconModule
  ],
  exports: [TpSelectgroupComponent, TpSelectitemComponent],
  declarations: [TpSelectgroupComponent, TpSelectitemComponent]
})

export class TpSelectgroupModule {}
export { TpSelectgroupComponent } from './TpSelectgroupComponent';
export { TpSelectitemComponent, TpSelectgroupItem } from './TpSelectitemComponent';
