import { Component, ContentChild, EventEmitter, Input, Output, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { TiDominatorComponent, TiDropComponent, TiDroplistComponent, TiFormComponent, TiLocale } from '@cloud/tiny3';

export interface TpRegionItem {
    /**
     * 当前大区的名称文本
     */
    displayName: string;
    /**
     * 大区的一组地区
     */
    regions?: Array<TpRegion>;
    /**
     * 大区的另一组地区，亚太区下某些需要特殊处理的区域
     */
    special?: Array<TpRegion>;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;
}

export interface TpRegion {
    /**
     * 当前区域的名称
     */
    displayName: string;
    disabled?: boolean;
    tipContent?: string;
    tipPosition?: string;
    /**
     * 灰化的原因
     */
    title?: string;
    /**
     * 设置选项的标志块
     *
     * 对象类型，包含两个属性:1.text: 显示的文本；2.class:标志的样式；eg:{text: string; class: string}
     */
    sup?: {
        text?: string;
        class?: string;
        /**
         * 允许有多余的属性字段
         */
        [propName: string]: any;
    };
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;
}

/**
 * 区域选择组件
 *
 * 组件提供了[TpRegionUtil]{@link ../classes/TpRegionUtil.html}供某些业务处理数据。
 *
 * <example-url>../tinyplus3demo/#/region/region-all</example-url>
 */
@Component({
    selector: 'tp-region',
    templateUrl: './region.html',
    styleUrls: ['./region.less'],
    providers: [TiFormComponent.getValueAccessor(TpRegionComponent)],
    host: {
        '(blur)': 'onBlur()'
    }
})
// TODO：和Select有重复代码，未来设法消除。
export class TpRegionComponent extends TiFormComponent {
  // TODO: add explicit constructor

  // TODO: add explicit constructor

    /**
     * 下拉面板展示的数据列表
     */
    @Input() items: Array<TpRegionItem>;
    /**
     * 预留文本
     */
    // tslint:disable-next-line: no-input-rename
    @Input('placeholder') placeholderText: string;
    /**
     * 选中事件，选中项没有变化
     */
    @Output() readonly select: EventEmitter<any> = new EventEmitter<any>();
    /**
     * 选中事件，选中项变化
     */
    // @Output() readonly change: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     */
    @ViewChild(TiDominatorComponent, { static: true }) TiDominatorCom: TiDominatorComponent;
    /**
     * @ignore
     */
    @ViewChild(TiDropComponent, {static: false}) dropCom: TiDropComponent;
    /**
     * @ignore
     */
    @ViewChild(TiDroplistComponent, {static: false}) droplistCom: TiDroplistComponent;
    /**
     * @ignore
     * 获取到用户自定义的按钮标志模板
     */
    @ContentChild('sup', { static: true }) public supTemplate: TemplateRef<any>;
   /**
    * @ignore
    * 灰化时默认提示文本
    */
    public dafaultDisableReason: string = TiLocale.getLocaleWords().tpRegion.unSupport;
    /**
     * @ignore
     * 是否为ctc局点，面板结构类似于hws，选块较小
     */
    public isSmallItem: boolean = TiLocale.getLocaleWords().tpRegionSmallItem;
    /**
     * @ignore
     * 是否为非hws非ctc局点，面板使用droplist，以select形式呈现
     */
    public isUseDroplist: boolean = TiLocale.getLocaleWords().tpRegionSelectStyle;
    /**
     * @ignore
     */
    public selectedItem: TpRegionItem;
    // ngOnInit(): void {
    //     // TODO：兼容disable和disabled
    // }
    ngOnChanges(changes: SimpleChanges): void {
        if (changes['items']?.currentValue?.length > 0 && this.model) {
            this.loopItemsFindSelectedItem();
        }
    }
    ngAfterViewInit(): void {
        super.ngAfterViewInit();
        if (this.isUseDroplist) {
            this.setFocusableElems(this.TiDominatorCom.getFocusableElems());
        } else {
            this.setFocusableElems(this.TiDominatorCom.getFocusableElems()
            .concat(this.dropCom.nativeElement));
        }
    }
    /**
     * @ignore
     */
    ngOnModelChange(): void {
        if (this.model && this.items?.length > 0) {
            this.loopItemsFindSelectedItem();
        } else {
            this.selectedItem = undefined;
        }
    }
    /**
     * @ignore
     */
    public onMousedown(event: MouseEvent): void {
        if (this.disabled) {
            return;
        }
        this.togglePanel();
    }
    /**
     * @ignore
     */
    public onClick(item: any, event: MouseEvent): void {
        if (item.disabled) {
            // 防止dominator 失焦blur
            event.preventDefault();

            return;
        }
        if (!this.model || this.model !== item) {
            this.model = item;
        } else {
            this.select.emit(item);
        }
        this.dropCom.hide();
    }
    /**
     * @ignore
     */
    public onBlur(): void {
        this.dropCom && this.dropCom.hide();
        this.droplistCom && this.droplistCom.hide();
    }
    /**
     * @ignore
     * 记录下拉面板展开状态
     */
    public get dropshow(): boolean {
        if (this.isUseDroplist) {
            return this.droplistCom ? this.droplistCom.isShow : false;
        } else {
            return  this.dropCom ? this.dropCom.isShow : false;
        }
    }
    /**
     * 面板的打开或关闭，统一处理drop和droplist
     */
    private togglePanel(): void {
        if (this.dropshow) {
            this.dropCom && this.dropCom.hide();
            this.droplistCom && this.droplistCom.hide();
        } else {
            this.dropCom && this.dropCom.show();
            this.droplistCom && this.droplistCom.show();
        }
    }
    private loopItemsFindSelectedItem(): void {
        this.items.some((item: TpRegionItem): boolean => {
            if (item.regions && this.findSelectedItem(item.regions, item)) {
                return true;
            } else if (item.special) {
                return this.findSelectedItem(item.special, item);
            }

            return false;
        });
    }

    private findSelectedItem(data: Array<TpRegion>, item: TpRegionItem): boolean {
        const flag: boolean =  data.some((region: TpRegion): boolean => {
            if (region === this.model) {
                this.selectedItem = item;

                return true;
            }

            return false;
        });

        return flag;
    }
}
