import { Component, Input, SimpleChange, SimpleChanges, ElementRef, Renderer2 } from '@angular/core';
import { TiBaseComponent, Util } from '@cloud/tiny3';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { share } from 'rxjs/operators';

// TODO：如果封装为胶囊样式，DOM内部多一层，比现在优劣？
/**
 *  Icon组件，非本地懒加载Icon
 *
 *  Icon组件可以设置显示Icon。
 *
 * <example-url>../tinyplus3demo/#/icon/icon-all</example-url>
 */
@Component({
    selector: 'tp-icon:not([local])',
    template: '<ng-content></ng-content>',
    styleUrls: ['./icon.less'],
    host: {
        '[class.tp-cloud-action-state-ing]': "name === 'cloud-action-state-ing'"
    },
})
export class TpIconComponent extends TiBaseComponent {

    /**
     * Icon基路径
     */
    private static path: string = 'assets/tinyplus3/icons/';
    /**
     * 保存url和观察者，避免并发的重复请求。
     */
    private static urlObservableMap: Map<String, Observable<string>> = new Map<String, Observable<string>>();
    /**
     * icon名称
     */
    @Input() name: string;

    constructor(protected hostRef: ElementRef, protected renderer: Renderer2, private http: HttpClient) {
        super(hostRef, renderer);
    }
    /**
     * 设置icon下载地址，默认是'assets/tinyplus3/icons/'
     * @param path
     */
    public static setPath(path: string): void {
        TpIconComponent.path = path;
    }

    ngOnChanges(changes: SimpleChanges): void {
        super.ngOnChanges(changes);
        const nameChange: SimpleChange = changes['name'];
        if (nameChange && !Util.isEmptyString(nameChange.currentValue)) {
            // 拼接URL
            const url: string = `${TpIconComponent.path}${nameChange.currentValue}.svg`;
            // 取出观察者
            let ob: Observable<string> = TpIconComponent.urlObservableMap.get(url);
            if (!ob) {
                // 如果不存在观察者，则发起http请求。share()是为了避免每次订阅都发起http请求。
                ob = this.http.get(url, { responseType: 'text' }).pipe(share());
                // 保存ur和观察者
                TpIconComponent.urlObservableMap.set(url,ob);
            }
            // 订阅http请求结果
            ob.subscribe(
                (svgText: string): void => {
                    // 将<svg>挂在<tp-icon>下
                    this.nativeElement.innerHTML = svgText;
                    // 删除url和观察者
                    TpIconComponent.urlObservableMap.delete(url);
                }
            );
        }
    }
}
