import { CfEndpoints, CfLinks, CfUser, EndpointsData, HttpService, LinksData, Mask, Message,
    StorageService, UserData } from '@cloud/cfdata';
import { TpDatePipe } from '../pipes/locale/TpLocaleModule';
//
/**
 * @ignore
 * 获取console相关的工具类
 * 兼容angular.js版本的console
 */
export class CfUtil {
    public static async getUser(): Promise<any> {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('$rootScope');
        }

        const cfUserData: CfUser = await UserData.getCfUser();

        return cfUserData;
    }

    public static async getLinks(): Promise<any> {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('$rootScope');
        }

        const cfLinksData: CfLinks = await LinksData.getLinks();

        return cfLinksData;
    }

    public static async getEndpoints(): Promise<any> {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('$rootScope');
        }

        const cfEndpointsData: CfEndpoints = await EndpointsData.getCfEndpoints();

        return cfEndpointsData;
    }

    public static getHttpService(): any {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('camel');
        }

        return HttpService;
    }

    public static getMessage(): any {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('msgService');
        }

        return Message;
    }

    public static getMask(): any {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('mask');
        }

        return Mask;
    }

    public static getStorageService(): any {
        if (this.isAngularJSConsole()) {
            return this.getFromAngularJSConsole('storage');
        }

        return StorageService;
    }

    public static formatLocaleDateTime(datetime: string | number | Date): any {
        if (this.isAngularJSConsole()) {
            const localeService: any = this.getFromAngularJSConsole('localeService');
            if (localeService &&  typeof localeService.formatLocaleDateTime === 'function') {
                return localeService.formatLocaleDateTime(datetime);
            } else {
                return datetime;
            }
        }

        return new TpDatePipe().transform(datetime, 'dateTimeZone');
    }

    public static isNonsupportRegion(): (nonsupportRegions: any, regionId: any, supportRegions: any) => boolean {
        if (this.isAngularJSConsole()) {
            const rootScope: any = this.getFromAngularJSConsole('$rootScope');

            return rootScope && rootScope.isNonsupportRegion;
        }

        return UserData.isNonsupportRegion;
    }

    public static getAppWebPath(): string {
        // 兼容appWebPath(旧版)、AppWebPath(新版)
        let appWebPath: string = (window as any).AppWebPath || (window as any).appWebPath;
        // 去掉首尾的/符号
        appWebPath = appWebPath && appWebPath.replace(/^\//, '')
            .replace(/\/$/, '');

        return `/${appWebPath}`;
    }

    private static isAngularJSConsole(): boolean {
        return !!(window as any).angular;
    }

    private static getFromAngularJSConsole(inject: string): any {
        const html: any = (window as any).angular.element('html');
        // 使用ng1+ng2 新融合方案时，Angular应用默认所在的根DOM元素为body
        const body: any = (window as any).angular.element('body');

        if (html && html.injector()) {
            return html.injector().get(inject);
        } else if (body && body.injector()) {
            return body.injector().get(inject);
        } else {
            return;
        }
    }
}
