import { Component, ElementRef, EventEmitter, Input, Output, Renderer2, ViewChild } from '@angular/core';
import { TiLocale, TiFormComponent, Util, TiDominatorComponent, TiDropsearchComponent, TiButtongroupComponent } from '@cloud/tiny3';
/**
 * @ignore
 * 规范提出，云业务通用场景不会使用该组件，10.1.3起，在官网隐藏该组件。
 */
export interface TpRegionselectOption {
    /**
     * 地区名称
     */
    label: string;
    /**
     * 地区对应序列ID
     */
    id: string;
    /**
     * 当前地区的下级地区列表
     */
    nextLevel?: Array<TpRegionselectOption>;
    /**
     * 允许有多余的属性字段
     */
    [propName: string]: any;
}
/**
 * @ignore
 * 规范提出，云业务通用场景不会使用该组件，10.1.3起，在官网隐藏该组件。
 *
 * regionselect组件提供省、市、县三级区域的选择
 *
 * <example-url>../tinyplus3demo/#/regionselect/regionselect-all</example-url>
 */
@Component({
    selector: 'tp-regionselect',
    templateUrl: './regionselect.html',
    styleUrls: ['./regionselect.less'],
    providers: [TiFormComponent.getValueAccessor(TpRegionselectComponent)],
    host: {
        '(blur)': 'onBlur()'
    }
})

export class TpRegionselectComponent extends TiFormComponent {
    constructor(protected hostRef: ElementRef, protected renderer: Renderer2) {
        super(hostRef, renderer);
    }
    /**
     * 区域选择总数据
     */
    @Input() regionselectOption: Array<TpRegionselectOption>; // 下拉总数据
    /**
     * 无数据时组件下展显示，需服务自行配置
     */
    @Input() noDataText: string = '...';
    /**
     * 是否支持搜索
     */
    @Input() searchable: boolean = false;
    /**
     * 区域点击选择之后触发该事件
     */
    @Output() readonly change: EventEmitter<any> = new EventEmitter<any>();
    /**
     * @ignore
     */
    @ViewChild(TiDominatorComponent, { static: true }) TiDominatorCom: TiDominatorComponent;
    /**
     * @ignore
     */
    @ViewChild(TiDropsearchComponent, { static: true }) dropCom: TiDropsearchComponent;
    /**
     * @ignore
     */
    @ViewChild(TiButtongroupComponent, { static: true }) btnGroupCom: TiButtongroupComponent;
    /**
     * @ignore
     */
    @ViewChild('wrapper', { static: false }) wrapperEle: ElementRef;
    /**
     * @ignore
     */
    @ViewChild('buttongroup', { static: false }) buttongroupEle: ElementRef;
    /**
     * @ignore
     */
    public labelKey: string = 'label';
    /**
     * @ignore
     */
    public showText: string;
    /**
     * @ignore
     */
    public btnItems: Array<any>;
    /**
     * @ignore
     */
    public btnSelected: any;
    /**
     * @ignore
     */
    public options: Array<TpRegionselectOption> = []; // 下拉实际数据,默认空数组
    /**
     * @ignore
     */
    public currentmodel: any;
    /**
     * @ignore
     */
    public dropshow: boolean; // 记录下拉面板展开状态
    private regionArr: Array<any> = [];
    private hasFoundId: boolean; // 标志量 是否根据id找到匹配项
    private isSpecialZone: boolean; // 标志量 是否为直辖市
    private isNotLastOne: boolean = true; // 标志量 是否为最后一级
    private classifications: Array<any>;
    private classificationLevel: number = 0; // 当前正在选择区域项的等级，省为0，市为1，区为2
    private currentClassificationsLength: number = 0; // 当前已选择区域项的等级，省为0，市为1，区为2
    private cfRegionSelectSeparativeSign: string = '-';
    private originSuggestions: Array<TpRegionselectOption>;
    private hasadjustWidth: boolean;
    private oldSearchable: boolean = undefined;
    /**
     * @ignore
     */
    writeValue(val: any): void {
        if (!val) {
            return;
        } else {
            this.model = val;
        }
        this.initSelectedData();
    }
    // tslint:disable-next-line:use-life-cycle-interface
    ngOnInit(): void {
        super.ngOnInit();
        this.classifications = [
            {
                id: 'province',
                text: TiLocale.getLocaleWords().tpRegionselect.regionSelect_province_label
            }, {
                id: 'city',
                text: TiLocale.getLocaleWords().tpRegionselect.regionSelect_city_label
            }, {
                id: 'country',
                text: TiLocale.getLocaleWords().tpRegionselect.regionSelect_county_label
            }
        ];
        this.model = Util.isUndefined(this.model) ? {} : this.model;
        this.hasadjustWidth = false;
        this.options = this.regionselectOption;
        this.getBtnItems();
        this.changePlaceholder();
        this.changeBtnRight();
        this.changeSelectedData();
    }
    // tslint:disable-next-line:use-life-cycle-interface
    ngAfterViewInit(): void {
        super.ngAfterViewInit();
        this.setFocusableElems(this.TiDominatorCom.getFocusableElems());
    }
    // tslint:disable-next-line:use-life-cycle-interface
    ngAfterViewChecked(): void {
        super.ngAfterViewChecked();
        if (this.searchable !== this.oldSearchable) {
            this.oldSearchable = this.searchable;
            if (this.searchable) {
                this.setFocusableElems(this.TiDominatorCom.getFocusableElems()
                    .concat(this.dropCom.getFocusableElems()));
            } else {
                this.setFocusableElems(this.TiDominatorCom.getFocusableElems());
            }
        }
    }
    /**
     * @ignore
     */
    public onMousedownFn(event: MouseEvent): void {
        event.preventDefault();  // 防止按钮聚焦
        event.stopPropagation();  // 阻止事件冒泡到document
    }
    /**
     * @ignore
     */
    public onBlur(): void {
        this.dropCom.hideWithoutFocus();
        this.dropshow = false;
    }
    /**
     * @ignore
     * 点击关闭或隐藏下拉面板
     */
    public onClickFn(event: MouseEvent): void {
        if (this.dropCom.isShow) {
            this.dropCom.hide();
            this.dropshow = false;
        } else {
            this.dropCom.show();
            this.dropshow = true;
            if (!this.hasadjustWidth) {
                const btns: NodeListOf<Element> = document.querySelectorAll('.ti3-btn-item');
                const extraWidth: number = this.getExtraWidth();
                const width: number = extraWidth / btns.length;
                btns.forEach((btn: any) => {
                    const oriWidth: number = btn.offsetWidth;
                    this.renderer.setStyle(btn, 'width', oriWidth + width + 'px');
                });
                this.hasadjustWidth = true;
            }
        }
    }
    /**
     * @ignore
     * 选择数据之后触发事件
     */
    public onSelect(option: any): void {
        this.onSelectFn(option);
        this.change.emit(option);
        const event: any = window.event;
    }

    private onSelectFn(selectedRegion: TpRegionselectOption): void {
        const currentBtn: any = this.btnItems[this.classificationLevel]; // 当前准备选择的按钮等级
        currentBtn.selectedText = selectedRegion.label; // 等级名称获取
        // 判断选择项是否是曾经已选择项
        const isChosenOption: boolean = this.btnItems.some((btnItem: any) => {
            return btnItem.selectedId === selectedRegion.id;
        });
        if (!isChosenOption) {
            this.currentClassificationsLength = this.btnItems.findIndex((btnItem: any) =>
                btnItem.id === this.btnSelected.id); // 获取当前已选择的级别（0、1、2）
            this.btnItems.forEach((item: any, index: number) => {
                if (index > this.currentClassificationsLength) {
                    item.selectedText = item.text;
                    item.originId = '';
                    item.selectedId = '';
                }
            });
        }

        this.btnItems[this.classificationLevel].selectedId = selectedRegion.id;
        this.changeSelectedData();
        this.isNotLastOne = this.classificationLevel < this.btnItems.length - 1;
        if (this.isNotLastOne) {
            this.classificationLevel++;
            this.currentClassificationsLength++;
            this.btnItems[this.classificationLevel].originId = this.btnItems[this.classificationLevel].originId || selectedRegion.id;
            this.handleOriginSuggestions();
            this.changeDropdownSuggestions();

            /**
             * 1、当子选项只有一项，且子选项id与当前项id相同，证明为同一区域级别
             * 2、同一区域级别情况下，如果子选项不是最后一级，证明为直辖市，需要自动跳过子选项的选择
             * 3、同一区域级别情况下，如果子选项是最后一级，证明为直辖县，则正常显示
             */
            if (this.originSuggestions.length === 1 &&
                (this.originSuggestions[0].id === selectedRegion.id
                    && this.classificationLevel < this.btnItems.length - 1)) {
                this.btnItems[this.classificationLevel].noDataDisable = true;
                this.onSelectFn(this.originSuggestions[0]);
            } else {
                this.btnItems[this.classificationLevel].noDataDisable = false;
                this.btnSelected = this.btnItems[this.classificationLevel];
                this.btnChangeFn();
            }
        } else {
            // 点击最低级别地区之后，面板会收起
            this.dropshow = false;
        }
        this.changePlaceholder();
        this.changeBtnRight();
    }

    // 当选择区域后，会更改selectedData数据
    private changeSelectedData(): void {
        const selectedTextArr: Array<any> = [];
        this.btnItems.forEach((item: any, index: number) => {
            const hasChosen: boolean = item.selectedText !== item.text;
            if (hasChosen) {
                if (index === this.classificationLevel) {
                    this.model[item.id] = {
                        id: item.selectedId,
                        label: item.selectedText
                    };
                }
                if (item.selectedText && !item.noDataDisable) {
                    selectedTextArr.push(item.selectedText);
                }
                this.model.id = item.selectedId;
                this.model.level = index;
            } else {
                this.model[item.id] = {};
            }
        });
        this.model.text = selectedTextArr.join(this.cfRegionSelectSeparativeSign);
        // 改变model地址触发modelchange事件发生
        this.model = {...this.model};
    }

    // 获取按钮配置项
    private getBtnItems(): void {
        this.btnItems = this.classifications.map((classification: any) => {
            classification.selectedText = classification.text;

            return classification;
        });
        this.btnSelected = this.btnItems[0];
    }
    // 根据selected初始配置,逻辑处理
    private initSelectedData(): void {
        if ((this.model && !this.model.id) || !this.regionselectOption || !this.regionselectOption.length) {
            return;
        }
        const regionIdArr: Array<any> = this.getRegionIdArr();
        if (regionIdArr.length) {
            this.getBtnItems();
            this.btnChangeFn();
            for (const item of regionIdArr) {
                this.onSelectFn(item);
            }
        }
    }
    // tslint:disable-next-line:typedef
    private getRegionIdArr() {
        this.getSelectedOption(this.regionselectOption);

        return this.regionArr;
    }
    // 根据selectedId 获取已选择数据详情
    private getSelectedOption(items: Array<any>): void {
        this.hasFoundId = false;
        this.regionArr = [];
        // tslint:disable-next-line:prefer-for-of
        for (let i: number = 0; i < items.length; i++) {
            if (items[i].id === this.model.id) {
                this.hasFoundId = true;
                const nextLevel: Array<TpRegionselectOption> = items[i].nextLevel;
                if (nextLevel && nextLevel.length && nextLevel[0].id === items[i].id) {
                    this.isSpecialZone = true;
                }
                if (this.isSpecialZone && !items[i].nextLevel[0].nextLevel) { // 直辖县
                    this.regionArr.unshift({
                        id: items[i].id,
                        label: items[i].label
                    });
                }
                this.regionArr.unshift({
                    id: items[i].id,
                    label: items[i].label
                });
                break;
            }
            if (items[i].nextLevel && items[i].nextLevel.length) {
                this.getSelectedOption(items[i].nextLevel);
                if (this.hasFoundId) {
                    this.regionArr.unshift({
                        id: items[i].id,
                        label: items[i].label
                    });
                    break;
                }
            }
        }
    }
    /**
     * @ignore
     */
    public btnChangeFn(): void {
        const selectedBtn: any = this.btnItems.find((item: any) => item.id === this.btnSelected.id);
        this.classificationLevel = this.btnItems.findIndex((item: any) => item === selectedBtn);
        this.isNotLastOne = this.classificationLevel < this.btnItems.length - 1;
        this.handleOriginSuggestions();
        this.changeDropdownSuggestions();
        if (this.model && this.model[selectedBtn.id]) {
            const index: number = this.options.findIndex((item: any) => item.id === this.model[selectedBtn.id].id);
            this.currentmodel = this.options[index];
        }
    }

    private findIndex(): number {
        return this.options.findIndex((item: any) => item.id === this.model.id);
    }

    private handleOriginSuggestions(): void {
        let dropdownSuggestions: Array<any> = this.regionselectOption;
        this.originSuggestions = [];
        this.btnItems.forEach((item: any, index: number) => {
            if (index > this.classificationLevel) {
                return;
            }
            const currentData: any = dropdownSuggestions.find((dropdown: any): boolean => {
                return dropdown.id === item.originId;
            });
            if (currentData && currentData.nextLevel && currentData.nextLevel.length) {
                dropdownSuggestions = currentData.nextLevel;
            }
        });
        if (!dropdownSuggestions) {
            return;
        }
        // 根据id从小到大进行排序
        dropdownSuggestions = dropdownSuggestions.sort((a: any, b: any) => {
            return a.id - b.id;
        });
        dropdownSuggestions.forEach((item1: any) => {
            this.originSuggestions.push({
                id: item1.id,
                label: item1.label,
                nextLevel: item1.nextLevel
            });
        });
    }

    // 根据originId从原始数据中第一级往下寻找
    private changeDropdownSuggestions(): void {
        this.options = this.originSuggestions;
    }

    // 更改选择区域显示文本
    private changePlaceholder(): void {
        let showText: string = '';
        this.btnItems.forEach((item: any, index: number) => {
            if (item.noDataDisable) {
                return;
            }
            const text: string = item.selectedText + ' ';
            if (index === 0) {
                showText += text;
            } else {
                // tslint:disable-next-line:prefer-template
                showText += this.cfRegionSelectSeparativeSign + ' ' + text;
            }
        });
        this.showText = showText;
    }

    // 判断按钮组disable权限
    private changeBtnRight(): void {
        this.btnItems.map((item: any, index: number) => {
            item.disabled = (index > this.currentClassificationsLength) || item.noDataDisable;

            return item;
        });
    }
    /**
     * @ignore
     * 判断是否溢出并获取超出宽度
     */
    public getExtraWidth(): number {
        const wrapperWidth: number = this.wrapperEle.nativeElement.offsetWidth;
        const buttonsWidth: number = this.buttongroupEle.nativeElement.offsetWidth;
        const isOverflow: boolean = wrapperWidth > buttonsWidth;
        if (!isOverflow) {
            return 0;
        } else {
            const extraWidth: number = wrapperWidth - buttonsWidth;

            return extraWidth;
        }
    }
}
