import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { TiFormComponent } from '@cloud/tiny3';

/**
 *  Icon组件
 *
 *  Icon组件可以设置显示Icon。
 *
 * <example-url>../tinyplus3demo/#/iconaction/iconaction-all</example-url>
 */
@Component({
    selector: 'tp-iconaction',
    templateUrl: './iconaction.html',
    styleUrls: ['./iconaction.less']
})
export class TpIconactionComponent extends TiFormComponent {
  // TODO: add explicit constructor

  // TODO: add explicit constructor

    /**
     * icon名称
     */
    @Input() iconName: string = 'cloud-action-add2';
    /**
     * icon名称
     */
    @Input() label: string;
    /**
     * 跳转链接
     *
     * 10.1.2新增
     */
    @Input() href: string;
    /**
     * 跳转方式
     *
     * 10.1.2新增
     */
    @Input() target: string = '_blank';
    /**
     * @ignore
     */
    @ViewChild('a', { static: true }) aRef: ElementRef;
    // tslint:disable-next-line: use-life-cycle-interface
    ngOnInit(): void {
        // 基类中做了设置宿主id的操作
        super.ngOnInit();
        this.setFocusableElems([this.aRef.nativeElement]);
    }
    /**
     * @ignore
     */
    public onClickFn(event: Event): void {
        if(!this.href) {
            event.preventDefault();
        }
    }
    /**
     * @ignore
     * 不传href时，禁用右键打开新窗口功能
     */
    public oncontextmenu(): boolean {
        return !!this.href;
    }
}
