import { Component, Input } from '@angular/core';
import { TiBaseComponent } from '@cloud/tiny3';
/**
 * 情景引导组件
 *
 * <example-url>../tinyplus3demo/#/guides/guides-all</example-url>
 */
@Component({
    selector: 'tp-guides',
    template: `<ng-content></ng-content>`,
    styleUrls: ['./guides.less'],
    // host: {
    //     '[class.tp-guides-container]': 'true'
    // }
})

export class TpGuidesComponent extends TiBaseComponent {
    /**
     * 设置activeIndex属性，指定当前激活项，默认值为-1，表示无激活状态；激活状态从0开始，在设置值之前的guide为完成项，之后则为待办项。
     */
    @Input() activeIndex: number = -1;
}
